import cesiumUtil from '@/modules/cesium/common/buss/CesiumViewer'
import msgUtil from '@/common/util/MessageUtil'
import Constant from '@/common/util/ConstantUtil'
// import GV from '@/common/buss/GlobalVariables'
import Bus from '@/common/buss/Bus'
import shadowquery from "./shadowquery";
import {SunlightAnalysis} from './sunlightanalysis.js'


let sunla;
let shadowQuery, drawHandler, tooltip, viewer, shadowQueryPoints, positions;
let vueInstance = {};
const ENTITYPRE="SPACEANALYSISSUNLIGHT";


function init(vueinstance) {
	viewer = viewer || cesiumUtil.CesiumViewer.getInstance().viewer;
	vueInstance=vueinstance;
	//创建阴影查询对象
	// shadowQuery = new shadowquery.ShadowQuery(viewer);
}
//激活日照阴影分析
function shadowAnalysis() {
	clearShadowQuery();
	Bus.VM.$on(Bus.SignalType.Scene_Mouse_Left_Click,ondrawleftclick);
	Bus.VM.$on(Bus.SignalType.Scene_Mouse_Right_Click,ondrawrightclick);

}

function ondrawleftclick(e) {
	let wgs84pos=cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(e.position);
	if(wgs84pos==null) return;
	positions.push(wgs84pos);
	let cartesianposlist=positions.map(item=>{return item.ToCartesian()});
	if(positions.length==1) return;
	if(positions.length==2) {
		//画线
		cesiumUtil.CesiumViewer.getInstance().SetEntity(new Cesium.Entity({
			id: ENTITYPRE+"drawregion",
			polyline: {
				positions: cartesianposlist,
				material: new Cesium.Color(0, 1, 0, 1),
				width: 2,
				clampToGround: false
			}
		}));
	}
	else{
		//画多边形
		cesiumUtil.CesiumViewer.getInstance().SetEntity({
			id: ENTITYPRE+ "drawregion",
			polygon: {
				hierarchy: new Cesium.PolygonHierarchy(cartesianposlist),
				// fill:Cesium.Color.GREEN,
				outline:true,
				outlineColor:Cesium.Color.GREEN,
				material: Cesium.Color.YELLOW.withAlpha(0.6),
				// 这里不能用贴地polygon或polyline，否则会影响离屏渲染
				perPositionHeight:true,
				// classificationType: Cesium.ClassificationType.CESIUM_3D_TILE,
				// 这里不能用贴地polygon或polyline，会影响离屏渲染
				heightReference:Cesium.HeightReference.NONE,
				// heightReference:Cesium.HeightReference.CLAMP_TO_GROUND
				// perPositionHeight: true //该属性代表使用每个点的高程，如果不设置则会贴地
			}
		});
	}
}



function ondrawrightclick(e) {
	cesiumUtil.CesiumViewer.getInstance().RemoveEntities(ENTITYPRE);
	Bus.VM.$off(Bus.SignalType.Scene_Mouse_Left_Click,ondrawleftclick);
	Bus.VM.$off(Bus.SignalType.Scene_Mouse_Right_Click,ondrawrightclick);
	if(positions.length<3){
		msgUtil.messagePrompt('warning', '分析区域请至少绘制三个点');
		return;
	}

	let dateValue = vueInstance.selDate;
	let startTime = new Date(dateValue);
	startTime.setHours(Number(vueInstance.startTime.toString()));
	let endTime = new Date(dateValue);
	endTime.setHours(Number(vueInstance.endTime.toString()));
	// debugger
	if(sunla==null){
		sunla=new SunlightAnalysis({
			viewer:viewer,
			lonlatlist:
				positions.map(item=>{
				return [item.longitude,item.latitude]
			}),
			pointspan:vueInstance.spacing,
			pointbottomheight:vueInstance.bottomHeight,
			pointextrudeheight:vueInstance.extrudeHeight,
			showpolygon:false,
			timesetting:[startTime.getFullYear(),startTime.getMonth()+1,startTime.getDate(),startTime.getHours(),endTime.getHours()]
		})
	}
	else{
		sunla.UpdatePolygon(
			positions.map(item=>{
			return [item.longitude,item.latitude]
		})
		);
		// sunla.UpdateDateTime(startTime.getFullYear(),startTime.getMonth()+1,startTime.getDate(),startTime.getHours(),endTime.getHours())
	}
	// shadowQuery.timeInterval = 60;//时间间隔
	// shadowQuery.Update({
	// 	poilist:positions,
	// 	bottomheight:Number(vueInstance.bottomHeight),
	// 	extrudeheight:Number(vueInstance.extrudeHeight),
	// 	spacing:Number(vueInstance.spacing),//阴影率点的间距
	// 	startTime:Cesium.JulianDate.fromDate(startTime),
	// 	endTime:Cesium.JulianDate.fromDate(endTime)
	// })

}

function onleftclick(e) {
	sunla.GetSunlightCoverate(e.position,function (rate) {
		msgUtil.notifyPrompt('日照覆盖率', (rate*100).toString()+"%");
	})
}

//获取日照率
function getShadowTooltip() {
	if(sunla==null) return;
	// if (tooltip) {
	// 	tooltip.setVisible(false);
	// }
	// tooltip = msgUtil.createTooltip(viewer._element);
	Bus.VM.$on(Bus.SignalType.Scene_Mouse_Left_Click,onleftclick);
}

//关闭日照阴影分析
function clearShadowQuery(clearButton) {
	positions=[];
	Bus.VM.$off(Bus.SignalType.Scene_Mouse_Left_Click,ondrawleftclick);
	Bus.VM.$off(Bus.SignalType.Scene_Mouse_Right_Click,ondrawrightclick);
	cesiumUtil.CesiumViewer.getInstance().RemoveEntities(ENTITYPRE);
	if(sunla){
		sunla.Remove();
		sunla=null;
	}
	Bus.VM.$off(Bus.SignalType.Scene_Mouse_Left_Click,onleftclick);
}

//刷新日照分析
function updateShadowAnlysis(shadowQueryAtt) {
	if(sunla==null) return;
	if (!isNaN(shadowQueryAtt.startTime)) {
		//viewer.entities.removeAll();
		let startTime = new Date(shadowQueryAtt.selDate);
		startTime.setHours(Number(shadowQueryAtt.startTime));
		const timeset=sunla.timesetting;
		sunla.UpdateDateTime(startTime.getFullYear(),startTime.getMonth()-1,startTime.getDate(),startTime.getHours(),timeset[4])
		// shadowQuery.startTime = Cesium.JulianDate.fromDate(startTime);
	}
	if (!isNaN(shadowQueryAtt.endTime) ) {
		//viewer.entities.removeAll();
		let endTime = new Date(shadowQueryAtt.selDate);
		endTime.setHours(Number(shadowQueryAtt.endTime));
		const timeset=sunla.timesetting;
		sunla.UpdateDateTime(timeset[0],timeset[1],timeset[2],timeset[3],endTime.getHours())
		// shadowQuery.endTime = Cesium.JulianDate.fromDate(endTime);
	}
	if (!isNaN(shadowQueryAtt.bottomHeight)) {
		let bh = Number(shadowQueryAtt.bottomHeight);
		let eh = Number(shadowQueryAtt.extrudeHeight);
		sunla.Updatebottomheight(bh);
		// shadowQuery.qureyRegion({
		// 	position: shadowQueryPoints,
		// 	bottom: bh,
		// 	extend: eh
		// });
	}
	if (!isNaN(shadowQueryAtt.extrudeHeight)) {
		// let bh = Number(shadowQueryAtt.bottomHeight);
		let eh = Number(shadowQueryAtt.extrudeHeight);
		sunla.UpdateExtrudeheight(eh);
		// shadowQuery.qureyRegion({
		// 	position: shadowQueryPoints,
		// 	bottom: bh,
		// 	extend: eh
		// });
	}
	if (!isNaN(shadowQueryAtt.spacing) ) {
		sunla.Updatepointspan(Number(shadowQueryAtt.spacing))
		// shadowQuery.spacing = Number(shadowQueryAtt.spacing);
	}

}
function SetVueInstance(vueinstance) {
	vueInstance=vueinstance;
}
export default {
	shadowAnalysis,
	getShadowTooltip,
	clearShadowQuery,
	updateShadowAnlysis,

	init,
	SetVueInstance
}
