import cesiumUtil from '@/modules/cesium/common/buss/CesiumViewer'
import msgUtil from '@/common/util/MessageUtil'
import geoUtil from '@/common/util/GeoUtil'
import ConstantUtil from '@/common/util/ConstantUtil'
import Bus from '@/common/buss/Bus'
import Coordinates from '@/common/util/Coordinates'

let drawHandler,tooltip,viewer,linearEntity,linearLabelEntity;
let vueInstance = {};
let drawPoint = 0;
let allcartesians = [];
let drawEntities = [];
let limitedHeight = 0;

function onleftclick(pos) {
    viewer = viewer || cesiumUtil.CesiumViewer.getInstance().viewer;
	drawPoint++;
    //收集所有的点
    let cartpos = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(pos.position).ToCartesian();
    allcartesians.push(cartpos);
    let drawEntity = null;
    //画当前的点
    drawEntity = viewer.entities.add({
        id: 'points_' + drawPoint,
        position: cartpos,
        point: {
            pixelSize: 5,
            color: Cesium.Color.BLUE,
            disableDepthTestDistance: 0
        }
    });
    drawEntities.push(drawEntity);
    if(drawPoint > 1){
        let cartesians = [];
        for(let i = allcartesians.length - 2; i < allcartesians.length; i++){
            cartesians.push(allcartesians[i]);
        }
        drawEntity = viewer.entities.add({
            id: 'lines_' + (drawPoint - 1),
            polyline: {
                positions: cartesians,
                material: Cesium.Color.GREEN,
                width: 2,
            }
        });
        drawEntities.push(drawEntity);
    }
}

function onrightclick(e) {
	if (drawPoint < 3) {
		tooltip.setVisible(false);
		//drawHandler.polyline.show = false;
		vueInstance.limitHeightEnable = false;
		msgUtil.notifyPrompt("绘制限高分析失败", "请至少左键单击三个点之后再结束", 'warning');
        return;
	}
	drawPoint = 0;
    if(drawEntities.length > 0){
        for(let i = 0; i < drawEntities.length; i++){
            viewer.entities.remove(drawEntities[i]);
        }
        
    }
    tooltip.setVisible(false);
    //隔两秒后将限高多边形绘制出来
    window.setTimeout(function() {
        linearEntity = viewer.entities.add({
            polygon: {
                //hierarchy: Cesium.Cartesian3.fromDegreesArray(positions),
                hierarchy: allcartesians,
                height: limitedHeight,
                material: new Cesium.Color(1, 1, 0.20, 0.5),
                outline: true,
                outlineColor: Cesium.Color.RED,
                outlineWidth: 5
            }
        });
    }, 2000);
        
	Bus.VM.$off(Bus.SignalType.Scene_Mouse_Left_Click,onleftclick);
	Bus.VM.$off(Bus.SignalType.Scene_Mouse_Right_Click,onrightclick);
    Bus.VM.$off(Bus.SignalType.Scene_Mouse_Move,onmousemove);
}

function onmousemove(e){
    if(drawPoint < 3){
        tooltip.showAt(e.startPosition, '<p>点击绘制第' + (drawPoint + 1) + '个点</p>');
    } else {
        tooltip.showAt(e.startPosition, '<p>点击绘制第' + (drawPoint + 1) + '个点, 或者右击结束绘制</p>');
    }
    
}

//限高分析
