//清除限高分析
function clearLinearExtrude() {
    if(tooltip){
        tooltip.setVisible(false);
    }
    allcartesians = [];
	Bus.VM.$off(Bus.SignalType.Scene_Mouse_Left_Click,onleftclick);
	Bus.VM.$off(Bus.SignalType.Scene_Mouse_Right_Click,onrightclick);
    cesiumUtil.CesiumViewer.getInstance().clearDrawHandler();
    if(linearEntity)
        viewer.entities.remove(linearEntity);
        // viewer.entities.remove(linearLabelEntity);
}
function SetVueInstance(vueinstance) {
	vueInstance=vueinstance;
}

export default {
    linearExtrudeAna,
    clearLinearExtrude,
	SetVueInstance
}
