import cesiumUtil from '@/modules/cesium/common/buss/CesiumViewer'
import msgUtil from '@/common/util/MessageUtil'
import geoUtil from '@/common/util/GeoUtil'
import ConstantUtil from '@/common/util/ConstantUtil'
import * as turf from "@turf/turf";

import Bus from '@/common/buss/Bus'
import TerrainClipPlan from './TerrainClipPlan'

let drawHandler,tooltip,viewer,linearEntity,linearLabelEntity;
let vueInstance = {};
let drawPoint = 0;
let drawPoints = [];
let drawEntityId = "cutfillentity";
let drawLeftClicked = false;
let terrainClipPlan = null;

function onleftclick(e) {
	drawPoint++;
}
function onrightclick(e) {
	if (drawPoint < 3) {
		tooltip.setVisible(false);
		drawHandler.polyline.show = false;
		vueInstance.limitHeightEnable = false;
		msgUtil.notifyPrompt("绘制填挖方分析失败", "请至少左键单击三个点之后再结束", 'warning');
	}
	drawPoint = 0;
	Bus.VM.$off(Bus.SignalType.Scene_Mouse_Left_Click,onleftclick);
	Bus.VM.$off(Bus.SignalType.Scene_Mouse_Right_Click,onrightclick);
}
//填挖方分析
function cutExtrudeAna(cutExtrudeHeight) {
    this.clearCutExtrude();
    viewer = viewer || cesiumUtil.CesiumViewer.getInstance().viewer;

    tooltip = msgUtil.createTooltip(viewer._element);

    Bus.VM.$on(Bus.SignalType.Scene_Mouse_Left_Click,onMouseLeftClick);
    Bus.VM.$on(Bus.SignalType.Scene_Mouse_Move,onMouseMove);
    Bus.VM.$on(Bus.SignalType.Scene_Mouse_Right_Click,onMouseRight);

    terrainClipPlan = new TerrainClipPlan(viewer, {
        height: vueInstance.alt ,
        splitNum: 1000,
        bottomImg: '/images/cesium/SpaceAnalysis/dig_bottom.jpeg',
        wallImg: '/images/cesium/SpaceAnalysis/dig_side.jpeg',
    });
}
function onMouseLeftClick(e){
    let pos = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(e.position);
    if (!pos) return;

    let len = drawPoints.length;
    if (len > 0) {
        drawPoints[len - 1] = pos;
    }
    else {
        drawPoints.push(pos);
    }

    cesiumUtil.CesiumViewer.getInstance().SetEntity(new Cesium.Entity({
        id: drawEntityId + "poi" + len.toString(),
        position: pos.ToCartesian(),
        point: {
            pixelSize: 10,
            color: Cesium.Color.YELLOW,
            // scaleByDistance:new Cesium.NearFarScalar(0, 0, 1, 1),
            disableDepthTestDistance: 0
        }
    }));
    drawLeftClicked = true;
}
function onMouseMove(e){
    // tooltip提示
    let tip = "";
    switch (drawPoints.length) {
        case 0:
            tip = '<p>点击绘制第一个点</p>';
            break;
        case 2:
            tip = '<p>点击绘制第二个点</p>';
            break;
        case 3:
            tip = '<p>点击绘制第三个点</p>';
            break;
        default:
            tip = '<p>点击绘制点，或者右击结束绘制</p>';
            break;
    }
    tooltip.showAt(e.endPosition, tip);

	if(drawPoints.length === 0) return;
    let pos = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(e.endPosition);
    if (!pos) return;

    if(drawLeftClicked){
    	drawPoints.push(pos);
        drawLeftClicked = false;
	}else{
        drawPoints[drawPoints.length - 1] = pos;
	}

    //画点
    cesiumUtil.CesiumViewer.getInstance().SetEntity(new Cesium.Entity({
        id: drawEntityId + "poi" + drawPoints.length.toString(),
        position: pos.ToCartesian(),
        point: {
            pixelSize: 10,
            color: Cesium.Color.YELLOW,
            // scaleByDistance:new Cesium.NearFarScalar(0, 0, 1, 1),
            disableDepthTestDistance: 0
        }
    }));
    //画线
	let tempArr = drawPoints.slice(0,drawPoints.length-1);
    let cartesians =  tempArr.map(poi => {return poi.ToCartesian()});
    cartesians.push(pos.ToCartesian());
    cartesians.push(drawPoints[0].ToCartesian());
    cesiumUtil.CesiumViewer.getInstance().SetEntity(new Cesium.Entity({
        id: drawEntityId + "polyline0",
        polyline: {
            positions: cartesians,
            material: new Cesium.Color(0, 1, 0, 0.6),
            width: 2,
            // depthFailMaterial:this.Color.ToCesiumColor()
        }
    }));
}
function onMouseRight(e){
    if (drawPoints.length < 3){
        msgUtil.notifyPrompt("绘制填挖方分析失败", "请至少左键单击三个点之后再结束", 'warning');
        return;//三点才能组成面
    }
    let pos = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(e.position);
    if (!pos) return;

    Bus.VM.$off(Bus.SignalType.Scene_Mouse_Left_Click,onMouseLeftClick);
    Bus.VM.$off(Bus.SignalType.Scene_Mouse_Move,onMouseMove);
    Bus.VM.$off(Bus.SignalType.Scene_Mouse_Right_Click,onMouseRight);
    tooltip.setVisible(false);

    let points = drawPoints.map(p=> p.ToCartesian());
    terrainClipPlan.updateData(points);

    let formatP = [];
    let clockP = [...drawPoints];clockP.push(drawPoints[0]);
    let clockwiseRing = turf.lineString(clockP.map(p=> [p.longitude,p.latitude]));
    let bool = turf.booleanClockwise(clockwiseRing);
    if(bool){
        for (const p of drawPoints) {
            formatP.unshift(p);
        }
    }else{
        formatP = [...drawPoints];
    }

    let primitives = cesiumUtil.CesiumViewer.getInstance().viewer.scene.primitives;
    let primitivesLen = primitives.length;
    for(let i=0;i<primitivesLen;i++){
        let primitive = primitives.get(i);
        if(primitive._url){
            clip3Dtiles(primitive,formatP);
        }
    }
    // let allS3MLayers = GV.getScpLayers();
    // allS3MLayers.forEach(s3mlayer=>{
    //     console.log(s3mlayer);
    // });

    cesiumUtil.CesiumViewer.getInstance().RemoveEntities(drawEntityId + "poi");
    cesiumUtil.CesiumViewer.getInstance().RemoveEntities(drawEntityId + "polyline0");
}
function clip3Dtiles(tileSet,points){
    let clippingPlanes = [];
    let inverseTransform = getInverseTransform (tileSet);
    for(let i=0;i<points.length-1;i++){
        let p1 = points[i];
        let p2 = points[i+1];
        clippingPlanes.push(createPlane(p1,p2,inverseTransform));
    }
    clippingPlanes.push(createPlane(points[points.length-1],points[0],inverseTransform));

    tileSet.clippingPlanes=new Cesium.ClippingPlaneCollection({
        planes: clippingPlanes,
        // unionClippingRegions: true,
    })
}
function getInverseTransform (tileSet) {
    let transform
    let tmp = tileSet.root.transform
    if ((tmp && tmp.equals(Cesium.Matrix4.IDENTITY)) || !tmp) {
        // 如果root.transform不存在，则3DTiles的原点变成了boundingSphere.center
        transform = Cesium.Transforms.eastNorthUpToFixedFrame(tileSet.boundingSphere.center)
    } else {
        transform = Cesium.Matrix4.fromArray(tileSet.root.transform)
    }
    return Cesium.Matrix4.inverseTransformation(transform, new Cesium.Matrix4())
}
function getOriginCoordinateSystemPoint (point, inverseTransform) {
    let val = Cesium.Cartesian3.fromDegrees(point.longitude, point.latitude)
    return Cesium.Matrix4.multiplyByPoint(
        inverseTransform, val, new Cesium.Cartesian3(0, 0, 0))
}
function createPlane (p1, p2, inverseTransform) {
    // 将仅包含经纬度信息的p1,p2，转换为相应坐标系的cartesian3对象
    let p1C3 = getOriginCoordinateSystemPoint(p1, inverseTransform)
    let p2C3 = getOriginCoordinateSystemPoint(p2, inverseTransform)

    // 定义一个垂直向上的向量up
    let up = new Cesium.Cartesian3(0, 0, 10)
    //  right 实际上就是由p1指向p2的向量
    let right = Cesium.Cartesian3.subtract(p2C3, p1C3, new Cesium.Cartesian3())

    // 计算normal， right叉乘up，得到平面法向量，这个法向量指向right的右侧
    let normal = Cesium.Cartesian3.cross(right, up, new Cesium.Cartesian3())
    normal = Cesium.Cartesian3.normalize(normal, normal)

    //由于已经获得了法向量和过平面的一点，因此可以直接构造Plane,并进一步构造ClippingPlane
    let planeTmp = Cesium.Plane.fromPointNormal(p1C3, normal)
    return Cesium.ClippingPlane.fromPlane(planeTmp)
}
function isClockWise (latLngArr) {
    if (latLngArr.length < 3) {
        return null
    }
    if (latLngArr[0] === latLngArr[latLngArr.length - 1]) {
        latLngArr = latLngArr.slice(0, latLngArr.length - 1)
    }
    let latMin = { i: -1, val: 90 }
    for (let i = 0; i < latLngArr.length; i++) {
        let { latitude } = latLngArr[i]
        if (latitude < latMin.val) {
            latMin.val = latitude
            latMin.i = i
        }
    }
    let i1 = (latMin.i + latLngArr.length - 1) % latLngArr.length
    let i2 = latMin.i
    let i3 = (latMin.i + 1) % latLngArr.length

    let v2_1 = {
        lat: latLngArr[i2].latitude - latLngArr[i1].latitude,
        lng: latLngArr[i2].longitude - latLngArr[i1].longitude
    }
    let v3_2 = {
        lat: latLngArr[i3].latitude - latLngArr[i2].latitude,
        lng: latLngArr[i3].longitude - latLngArr[i2].longitude
    }
    let result = v3_2.longitude * v2_1.latitude - v2_1.longitude * v3_2.latitude
    // result>0 3-2在2-1的顺时针方向 result<0 3-2在2-1的逆时针方向 result==0 3-2和2-1共线，可能同向也可能反向
    return result === 0 ? (latLngArr[i3].longitude < latLngArr[i1].longitude) : (result > 0)
}

//清除限高分析
function clearCutExtrude() {
    Bus.VM.$off(Bus.SignalType.Scene_Mouse_Left_Click,onMouseLeftClick);
    Bus.VM.$off(Bus.SignalType.Scene_Mouse_Move,onMouseMove);
    Bus.VM.$off(Bus.SignalType.Scene_Mouse_Right_Click,onMouseRight);
    if(tooltip){tooltip.setVisible(false)}

    drawPoint = 0;
    drawPoints = [];
    if(terrainClipPlan)terrainClipPlan.clear();
    let primitives = cesiumUtil.CesiumViewer.getInstance().viewer.scene.primitives;
    let primitivesLen = primitives.length;
    for(let i=0;i<primitivesLen;i++){
        let primitive = primitives.get(i);
        if(primitive._url){
            primitive.clippingPlanes && primitive.clippingPlanes.removeAll();
        }
    }
}



function SetVueInstance(vueinstance) {
	vueInstance=vueinstance;
}
export default {
	cutExtrudeAna,
	clearCutExtrude,
	SetVueInstance
}
