import cesiumUtil from '@/modules/cesium/common/buss/CesiumViewer'
import msgUtil from '@/common/util/MessageUtil'
import Constant from '@/common/util/ConstantUtil'

import Bus from '@/common/buss/Bus'
import coordinate from '@/common/util/Coordinates'

var viewer,scene,handlerPoint,sightline;

var addViewFlag = false;//当前点击状态是否是 添加观察点;
var addTargetFlag = false;//当前点击状态是否是 添加目标点

var num = 0;//添加的目标点的点号
var couldRemove = false;//是否能移除目标点
var couldAddTarget=false;//是否能添加目标点
let startpos=null;
let preentityid = "visibilityanalysispre";
let entitylen = 0;
function GetCurrentPoiFlag() {
    return startpos==null?1:2;
}
function HandleLeftClick(windowpos, curflag) {
	let cartesianposition = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(windowpos).ToCartesian();
	if(cartesianposition == null) return;
	if(curflag == 1){	//选取观察点的状态
		startpos = cartesianposition;
		cesiumUtil.CesiumViewer.getInstance().RemoveEntityById(preentityid+"startpoi");
		cesiumUtil.CesiumViewer.getInstance().SetEntity(new Cesium.Entity({
            id: preentityid + "startpoi",
            position: cartesianposition,
            point: {
                pixelSize: 10,
                color: Cesium.Color.GREEN,
                // scaleByDistance:new Cesium.NearFarScalar(0, 0, 1, 1),
                disableDepthTestDistance: 0
            }
        }));
	} else if (curflag == 2){
		cesiumUtil.CesiumViewer.getInstance().SetEntity(new Cesium.Entity({
            id: preentityid + "endpoi",
            position: cartesianposition,
            point: {
                pixelSize: 10,
                color: Cesium.Color.BLUE,
                // scaleByDistance:new Cesium.NearFarScalar(0, 0, 1, 1),
                disableDepthTestDistance: 0
            }
        }));

		let nowcamera = cesiumUtil.CesiumViewer.getInstance().GetCamera();
        setcamera(startpos,cartesianposition);
        let find = false;
        //获取终点对应的屏幕坐标
        let endwindowpos = Cesium.SceneTransforms.wgs84ToWindowCoordinates(cesiumUtil.CesiumViewer.getInstance().GetViewerByContainerid().scene, cartesianposition);
        //获取能看到的最后的点
        let cartesianmid = new Cesium.Cartesian3();
        if(endwindowpos != null){
            let midwgs84 = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(endwindowpos);
            if(midwgs84 != null)
            {
                cartesianmid = midwgs84.ToCartesian();
                find = true;
            }
        }
        if(find == false){
            entitylen++;
            cesiumUtil.CesiumViewer.getInstance().SetEntity(new Cesium.Entity({
                id: preentityid + entitylen,
                polyline: {
                    positions: [new Cesium.Cartesian3(startpos.x,startpos.y,startpos.z), new Cesium.Cartesian3(cartesianposition.x,cartesianposition.y,cartesianposition.z)],
                    material: Cesium.Color.BLUE,
                    width: 2,
                    // depthFailMaterial:this.Color.ToCesiumColor()
                }
            }))
            cesiumUtil.CesiumViewer.getInstance().CameraGoTo(nowcamera.latitude, nowcamera.longitude, nowcamera.height,nowcamera.heading,nowcamera.pitch,nowcamera.roll);
            return;
        }
        //绘制线段 起点到能看到最后点为能看到的点，最后点到终点为不能看到的点
        //画线
        entitylen++;
        cesiumUtil.CesiumViewer.getInstance().SetEntity(new Cesium.Entity({
            id: preentityid + entitylen,
            polyline: {
                positions: [new Cesium.Cartesian3(startpos.x,startpos.y,startpos.z), new Cesium.Cartesian3(cartesianmid.x,cartesianmid.y,cartesianmid.z)],
                material: Cesium.Color.BLUE,
                width: 2,
                // depthFailMaterial:this.Color.ToCesiumColor()
            }
        }))
        entitylen++;
        cesiumUtil.CesiumViewer.getInstance().SetEntity(new Cesium.Entity({
            id: preentityid + entitylen,
            polyline: {
                positions: [new Cesium.Cartesian3(cartesianmid.x,cartesianmid.y,cartesianmid.z), new Cesium.Cartesian3(cartesianposition.x,cartesianposition.y,cartesianposition.z)],
                material: Cesium.Color.RED,
                width: 2,
                // depthFailMaterial:this.Color.ToCesiumColor()
            }
        }))
        cesiumUtil.CesiumViewer.getInstance().CameraGoTo(nowcamera.latitude, nowcamera.longitude, nowcamera.height,nowcamera.heading,nowcamera.pitch,nowcamera.roll);
	}
}

function HandleClearAnalysis(){
	startpos=null;
    for(let i=1;i<=entitylen;i++)
    {
        cesiumUtil.CesiumViewer.getInstance().RemoveEntityById(preentityid+i);
    }
    cesiumUtil.CesiumViewer.getInstance().RemoveEntityById(preentityid+"startpoi");
    cesiumUtil.CesiumViewer.getInstance().RemoveEntityById(preentityid+"endpoi");
    entitylen=0;
}


function setcamera(startcarpos,endcarpos) {
    let viewer = cesiumUtil.CesiumViewer.getInstance().GetViewerByContainerid();
    var localpoi0 = new Cesium.Cartesian3(0, 0, 0);
    var localpoi1 = coordinate.CoordinateLocal.FromCartesian(endcarpos, startcarpos);
    var displane=coordinate.CoordinateLocal.GetDistancePlane(localpoi1,localpoi0);
    var dis=coordinate.CoordinateLocal.GetDistance(localpoi1,localpoi0);
    var heading=0;
    if(localpoi1.x>=0){
        heading=Math.acos(localpoi1.y/displane);
    }
    else{
        heading=0-Math.acos(localpoi1.y/displane);
    }
    var pitch=Math.asin(localpoi1.z/dis);
    viewer.scene.camera.setView({
        destination: startcarpos,
        orientation: {
            heading: heading,
            pitch: pitch,
            roll: Cesium.Math.toRadians(0)
        }
    });


}



//笛卡尔转换为经纬度
function Cartesian2toDegrees(position) {
	var cartographic = Cesium.Cartographic.fromCartesian(position);
	var longitude = Cesium.Math.toDegrees(cartographic.longitude);
	var latitude = Cesium.Math.toDegrees(cartographic.latitude);
	var height = cartographic.height;

	return [longitude, latitude, height];
}

export default {
	HandleLeftClick,
    HandleClearAnalysis,
    //GetCurrentPoiFlag
}
