<template>
    <div id="SpecialEffects">
        <div id="SubMainFrame" class="position-fixed" v-show="false">
            <img v-for="(item,index) in subtools"
                 :src="item.checked==true?'images/cesium/SpecialEffects/'+item.imgfilename+'selected.svg':'images/cesium/SpecialEffects/'+item.imgfilename+'.svg'"
                 :title="item.name"
                 @click="item.checked=!item.checked">
            <div style="width:20px;height:1px;background:#ffffffb2;margin:auto;margin-top:9px;"></div>
            <img src="images/common/close.svg" style="width:12px;height:12px;margin:9px 11px;" @click="HiddenMenuWithAnamate"/>
        </div>
        <sunlight ref="sunlight" :winWidth="winWidth" @close="closesunlight"></sunlight>
    </div>
</template>

<script>
    import sunlight from '../../Sunlight/components/Sunlight'
	import Cesiumviewer from '@/modules/cesium/common/buss/CesiumViewer'


    export default {
		name: "SpecialEffects",

		data() {
			let subtools=[
				{name:"雨",imgfilename:"rain", _checked: false},
				{name:"雪",imgfilename:"snow", _checked: false},
				{name:"雾",imgfilename:"fog", _checked: false},
				{name:"日照",imgfilename:"sunlight", _checked: false},
            ];
			let that=this;
			subtools.forEach(data => {
				Object.defineProperty(data, "checked", {
					get: function () {
						return this._checked;
					},
					set:function (value) {
						if(value!=this._checked){
							this._checked=value;
							that.onsubmenucheckchanged(this);
						}
					}
				})
			})
			return {


				subtools:subtools,

            };
		},
		props:['winWidth'],
        components:{sunlight},
        computed: {

        },
        mounted() {

        },
		methods: {
			ShowMenu(){
				$("#SpecialEffects #SubMainFrame").css('display', 'block');
				$("#SpecialEffects #SubMainFrame").animate({height: '190'});
			},
			HiddenMenuWithAnamate(){
				$("#SpecialEffects #SubMainFrame").animate({height: '0px'}, '400', function () {
					$("#SpecialEffects #SubMainFrame").css('display', 'none');
				});
				this.clearanasubmenus();
				this.$emit("close");
			},
			HiddenMenu(){
				$("#SpecialEffects #SubMainFrame").css('display', 'none');
				$("#SpecialEffects #SubMainFrame").css('height', '0px');
				this.clearanasubmenus();
			},
			clearanasubmenus(){
				this.subtools.forEach(subtool=>{
					subtool.checked=false;
				})
                this.$refs.sunlight.isShow=false;
			},
			onsubmenucheckchanged(subitem){
				switch (subitem.name) {
                    case "雨":
						Cesiumviewer.CesiumViewer.getInstance().SetRainVisible(subitem.checked);
                    	break;
                    case "雪":
						Cesiumviewer.CesiumViewer.getInstance().SetSnowVisible(subitem.checked);
                    	break;
                    case "雾":
						Cesiumviewer.CesiumViewer.getInstance().SetFogVisible(subitem.checked);
                    	break;
                    case "日照":
						this.$refs.sunlight.isShow=!this.$refs.sunlight.isShow;
                    	break;
                    default:
                    	break;
				}


			},
			closesunlight(){
				let tool=this.subtools.find(tool=>tool.name=="日照");
				tool.checked=false;
            }
        },
		watch: {}
	}
</script>

<style scoped>

</style>
<style>
    #SpecialEffects #SubMainFrame {
        background: #122134cc;
        width: 34px;
        /*height: 268px;*/
        height:0px;
        top: 100px;
        right: 22px;
    }

    #SpecialEffects #SubMainFrame img {
        width: 20px;
        height: 20px;
        margin: 9px 7px;
        box-sizing: border-box;
        vertical-align: top;
    }

</style>
