	import dialogcomwithborder from '@/common/components/dialogcomwithborder'
	import basemixins from '@/common/buss/BaseMixins'
	import visualFieldHandle from '../buss/visualFieldHandler'


    let viewshed3Datt = {};

	export default {
		name: "VisualField",

		data() {
			return {

				visualFieldEnable: false,
				viewshed3DAddHeight: 2,
                showMoreFlag: false,
                direction: 1,
                pitch: 0,
                distance: 1,
                horizontalFov: 1,
                verticalFov: 1,

                maxdistance:500
			}
		},
        computed:{
            distancemarks:function () {
                let result={};
                result[1]='1';
                result[this.maxdistance]=this.maxdistance.toString();
                return result;
            },
            left:function () {
                return (this.winWidth - 34 - 20 - 400 - 22);
            }
        },
		mixins: [basemixins.DATA_ISSHOW],
		components: {dialogcomwithborder},
        props:['winWidth'],
		created() {
			//visualFieldHandle.vueInstance = this;
            visualFieldHandle.SetVueInstance(this);
		},
		methods: {
			getViewField() {
				this.visualFieldEnable = !this.visualFieldEnable;
                // this.showMoreFlag = false;
				visualFieldHandle.getViewshed3D(this.viewshed3DAddHeight);
			},
			clearViewField() {
				this.visualFieldEnable = false;
				visualFieldHandle.clearViewshed3D();
			},
            close(){
				this.isShow=false;
				this.$emit('onclose');
            },
            visualChange(){
			    if(!this.showMoreFlag){return}
                viewshed3Datt.direction = this.direction;
                viewshed3Datt.pitch = this.pitch;
                viewshed3Datt.distance = this.distance;
                viewshed3Datt.horizontalFov = this.horizontalFov;
                viewshed3Datt.verticalFov = this.verticalFov;
                visualFieldHandle.refreshViewshed3D(viewshed3Datt);
            }
		},
		watch: {
			isShow(newval,oldval){
				if(newval==false){
					this.clearViewField();
                }
            },
			viewshed3DAddHeight(newval, oldval) {
				if (isNaN(newval) || newval == '' || newval < 0 || !this.visualFieldEnable) return;
				clearTimeout(this.timeout);
				this.timeout = setTimeout(() => {
					let Datt = {};
                    Datt.viewshed3DAddHeight = Number(newval);
					visualFieldHandle.refreshViewshed3D(Datt);
				}, 300);
			}
		},

