 * Description 填挖方分析
-->
<template>
    <div >
        <dialogcomwithborder id="cutfillcom" title="填挖方"
                             :width="400" :height="124" :left="left" :top="100"
                             headertitlestyle="left:20px;transform:translate(0px,0px);line-height:40px;font-size:16px;font-weight:bold;"
                             v-show="isShow" :headhaloshow="false" :onclose-callback="close">
            <template v-slot:title>
                <img src="images/cesium/SpaceAnalysis/HeightLimitAnalysis.svg" class="float-left" style="width:20px;height:20px;margin:10px 8px 10px 0px;">
                <div class="float-left">填挖方</div>
            </template>
            <div id="cutfillcom_Content">
                <el-button class="float-left" :class="cutFillEnable?'select':''" style="width:100px;" @click="cutFillAnalysis">绘制范围</el-button>
                <el-button style="width:30px;margin:0px 30px;" class="spatialanalysisclear float-left" @click="clearCutFill"></el-button>
                <div class="float-left FontSize-13px"  style="line-height: 30px;">高程值</div>
                <el-input class="float-left" v-model="alt"></el-input>
                <div class="float-left FontSize-13px" style="line-height: 30px;margin-left:8px;">米</div>
            </div>
        </dialogcomwithborder>
    </div>
</template>

<script>
	import dialogcomwithborder from '@/common/components/dialogcomwithborder'
	import basemixins from '@/common/buss/BaseMixins'
	import cutfillhandler from '../buss/cutfillhandler'
	import msgUtil from '@/common/util/MessageUtil'
	export default {
		name: "cutfillcom",
		components: {dialogcomwithborder},
		mixins: [basemixins.DATA_ISSHOW],
		props: ['winWidth'],
		data() {
			return {


				cutFillEnable: false,
				alt:100,
            };
		},
		computed: {

			left:function () {
				return (this.winWidth - 34 - 20 - 400 - 22);
			}
        },
        mounted() {

        },
		created() {
			cutfillhandler.SetVueInstance(this);
		},

		methods: {
			cutFillAnalysis(){
                //执行分析前判断阈值合法性
				if (this.judgeThreshold(this.alt)) {
					this.cutFillEnable = !this.cutFillEnable;
					cutfillhandler.cutExtrudeAna(this.alt);
				}
            },
			clearCutFill(){
				this.cutFillEnable = false;
				cutfillhandler.clearCutExtrude();
            },
			checkNum(value) {
				return !isNaN(value);
			},
			judgeThreshold(value) {
				if (!value) {
					msgUtil.messagePrompt('warning', '请设置高程值');
					return false;
				} else if (!this.checkNum(Number(value))) {
					msgUtil.messagePrompt('warning', '高程值格式非法');
					return false;
				}
				// else if (Number(value) <= 0) {
				// 	msgUtil.messagePrompt('warning', '高程值必须大于零');
				// 	return false;
				// }
				else {
					return true;
				}
			},
			close(){
				this.isShow=false;
				this.$emit('onclose');
			},
        },
		watch: {
			isShow(newval,oldval){
				if(newval==false){
					this.clearCutFill();
				}
			},
			cutFillEnable(newval, oldval) {
				if (newval)
					this.$emit("clearOtherAnalysis");
			}
        }
	}
</script>

<style scoped>

</style>

<style>
    #cutfillcom #cutfillcom_Content{
        width:334px;
        height:30px;
        margin:auto;
        color:#ffffffb2;
    }
    #cutfillcom #cutfillcom_Content button{
        height:30px;
        line-height: 30px;
        padding:0px;
        border-radius: 2px;
        border:none;
        background-color: #049effe5;
        color:white;
        box-sizing: border-box;
        margin-left:0px;
    }

    #cutfillcom #cutfillcom_Content button.select {
        background-color: #049eff;
    }

    #cutfillcom #cutfillcom_Content .el-input{
        width: auto;
        height:auto;
        margin-left:8px;
    }
    #cutfillcom #cutfillcom_Content .el-input__inner{
        background: transparent;
        width: 60px;
        height: 30px;
        line-height: 30px;
        padding: 0 4px;
        color: #ffffffb2;
    }
</style>
