import cesiumUtil from '@/modules/cesium/common/buss/CesiumViewer'
import extractskyline from './extractSkyline'


//提取天际线
function extractSkyline() {
    let viewer = cesiumUtil.CesiumViewer.getInstance().viewer;
    this.collection = viewer.scene.postProcessStages;
    this.edgeDetection=new Cesium.PostProcessStage({
        name : 'czm_edge_detection_' + "ddddddd",
        fragmentShader :  'uniform sampler2D colorTexture;\n' +
            'uniform sampler2D depthTexture;\n' +
            'uniform float length;\n' +
            'uniform vec4 color;\n' +
            '\n' +
            'varying vec2 v_textureCoordinates;\n' +
            '\n' +
            'void main(void)\n' +
            '{\n' +
            '    float directions[3];\n' +
            '    directions[0] = -1.0;\n' +
            '    directions[1] = 0.0;\n' +
            '    directions[2] = 1.0;\n' +
            '\n' +
            '    float scalars[3];\n' +
            '    scalars[0] = 3.0;\n' +
            '    scalars[1] = 10.0;\n' +
            '    scalars[2] = 3.0;\n' +
            '\n' +
            '    float padx = 1.0 / czm_viewport.z;\n' +
            '    float pady = 1.0 / czm_viewport.w;\n' +
            '\n' +
            '#ifdef CZM_SELECTED_FEATURE\n' +
            '    bool selected = false;\n' +
            '    for (int i = 0; i < 3; ++i)\n' +
            '    {\n' +
            '        float dir = directions[i];\n' +
            '        selected = selected || czm_selected(vec2(-padx, dir * pady));\n' +
            '        selected = selected || czm_selected(vec2(padx, dir * pady));\n' +
            '        selected = selected || czm_selected(vec2(dir * padx, -pady));\n' +
            '        selected = selected || czm_selected(vec2(dir * padx, pady));\n' +
            '        if (selected)\n' +
            '        {\n' +
            '            break;\n' +
            '        }\n' +
            '    }\n' +
            '    if (!selected)\n' +
            '    {\n' +
            '        gl_FragColor = vec4(color.rgb, 0.0);\n' +
            '        return;\n' +
            '    }\n' +
            '#endif\n' +
            '\n' +
            '    float horizEdge = 0.0;\n' +
            '    float vertEdge = 0.0;\n' +
            '\n' +
            '    for (int i = 0; i < 3; ++i)\n' +
            '    {\n' +
            '        float dir = directions[i];\n' +
            '        float scale = scalars[i];\n' +
            '\n' +
            '        horizEdge -= texture2D(depthTexture, v_textureCoordinates + vec2(-padx, dir * pady)).x * scale;\n' +
            '        horizEdge += texture2D(depthTexture, v_textureCoordinates + vec2(padx, dir * pady)).x * scale;\n' +
            '\n' +
            '        vertEdge -= texture2D(depthTexture, v_textureCoordinates + vec2(dir * padx, -pady)).x * scale;\n' +
            '        vertEdge += texture2D(depthTexture, v_textureCoordinates + vec2(dir * padx, pady)).x * scale;\n' +
            '    }\n' +
            '\n' +
            '    float len = sqrt(horizEdge * horizEdge + vertEdge * vertEdge);\n' +
            '    vec4 origincolor = texture2D(colorTexture, v_textureCoordinates);\n' +
            '    gl_FragColor = vec4(color.rgb, len > length ? color.a : 0.0);\n' +
           // '    gl_FragColor = len > length?color:origincolor;\n' +
            '}\n',
        uniforms : {
            length : 0.1,
            color : Cesium.Color.clone(Cesium.Color.RED)
        }
    });
   // let edgeDetection = Cesium.PostProcessStageLibrary.createEdgeDetectionStage();
    this.postProccessStage1 = new Cesium.PostProcessStage({
        name: 'czm_skylinetemp',
        fragmentShader: 'uniform sampler2D colorTexture;' +
            'uniform sampler2D depthTexture;' +
            'varying vec2 v_textureCoordinates;' +
            'void main(void)' +
            '{' +
            'float depth = czm_readDepth(depthTexture, v_textureCoordinates);' +
            'vec4 color = texture2D(colorTexture, v_textureCoordinates);' +
           // 'gl_FragColor = texture2D(depthTexture, v_textureCoordinates);'+
            'if(depth<1.0 - 0.000001){'+
            'gl_FragColor = color;' +
            '}'+
            'else{'+
            'gl_FragColor = vec4(1.0,0.0,0.0,1.0);'+
            '}'+
            '}',
        // uniforms:{
        //     v_textureCoordinates: vec2
        // }
    });
    let postProccessStage2 = new Cesium.PostProcessStage({
        name: 'czm_skylinetemp1',
        fragmentShader: 'uniform sampler2D colorTexture;' +
            'uniform sampler2D redTexture;' +
            'uniform sampler2D silhouetteTexture;' +
            'varying vec2 v_textureCoordinates;' +
            'void main(void)' +
            '{' +
            'vec4 redcolor=texture2D(redTexture, v_textureCoordinates);'+
            'vec4 silhouetteColor = texture2D(silhouetteTexture, v_textureCoordinates);' +
            'vec4 color = texture2D(colorTexture, v_textureCoordinates);' +
            'if(redcolor.r == 1.0){'+
            'gl_FragColor = mix(color, vec4(1.0,0.0,0.0,1.0), silhouetteColor.a);' +
            '}'+
            'else{'+
            'gl_FragColor = color;'+
            '}'+
            '}',
        uniforms: {
            redTexture: this.postProccessStage1.name,
            silhouetteTexture: this.edgeDetection.name
        }
    });
    this.skyLineStage = new Cesium.PostProcessStageComposite({
        name: 'czm_skyline',
        stages: [this.edgeDetection, this.postProccessStage1, postProccessStage2],
        //stages:[postProccessStage1, postProccessStage2],
        inputPreviousStageTexture: false,
        uniforms: this.edgeDetection.uniforms
    });
    this.collection.add(this.skyLineStage);
   // this.collection.add(this.edgeDetection);
    //this.collection.add(this.postProccessStage1)
}

