    //     // destroyAttachments: false,
    // });




    //使用自适应 跨越整个彩色范围
    sunlightanalysis.colormodels = ColorModel.GetRGBRanks_FromBlueToRed(juliantimelist.length - 1);
    let cameras = [];
    let depthtextures = [];

    let framebuffers = [];
    let transformviewmatrixs = [];
    let tfnopromats = [];
