SunlightAnalysis.prototype.UpdatePolygon = function (lonlatlist) {
    this.lonlatlist = lonlatlist || this.lonlatlist;
    refresh(this);
}

SunlightAnalysis.prototype.Updatepointspan = function (pointspan) {
    this.pointspan = pointspan || this.pointspan;
    refresh(this);
}

SunlightAnalysis.prototype.Updatebottomheight = function (bottomheight) {
    this.pointbottomheight = bottomheight || this.pointbottomheight;
    refresh(this);
}
SunlightAnalysis.prototype.UpdateExtrudeheight = function (extrudeheight) {
    this.pointextrudeheight = extrudeheight || this.pointextrudeheight;
    refresh(this);
}

// timesetting:日照分析时间设置，可选，默认为：[2023,12,22,6,18] 代表分析2023-12-22这一天的日照覆盖率，从6点到18点
SunlightAnalysis.prototype.UpdateDateTime = function (year, month, day, starthour, endhour) {
    this.timesetting = [year || this.timesetting[0], month || this.timesetting[1], day || this.timesetting[2], starthour || this.timesetting[3], endhour || this.timesetting[4]];
    refresh(this);
}


SunlightAnalysis.prototype.Updatepointsize = function (pointsize) {
    this.pointsize = pointsize || this.pointsize;
    refresh(this);
}

