SunlightAnalysis.prototype.Remove=function () {
    if (this.postrender != null){
        this.viewer.scene.postRender.removeEventListener(this.postrender);
    }
    if(this.preupdatelis){
        this.viewer.scene.preUpdate.addEventListener(this.preupdatelis);
    }
    if(this.polygonprim){
        this.viewer.scene.primitives.remove(this.polygonprim);
    }
    if(this.pointprimitive){
        this.viewer.scene.primitives.remove(this.pointprimitive);
    }
    this.removed=true;
}

