<template>
  <vue-draggable-resizable-gorkys ref="draggabledialog" class="partcomwithborder" :class="classname" :w=width
                                  :h="height" :x="left" :y="top" :resizable=resizable :draggable="draggable"
                                  v-on:resizing="onResize">
    <div class="dialogcomwithbordersubcontent width100percent height100percent" :class="subclassname"
         :style="headervisible?'':'padding-top:0px;'">
      <div class="dialogcomwithborder-header width100percent" v-show="headervisible" >
        <div class="color-white FontSize-18px horizon-center position-absolute dialogcomwithbordertitle" :style="headertitlestyle">
          <slot name="title">{{title}}</slot>
        </div>
        <input type="image" class="dialogcomwithborderclose" src="images/common/close.svg" @click="close"
               v-show="closebtnshow">
        <img :src="headerhaloimgsrc"
             class="width100percent verticlealign-bottom position-relative dialogcomwithborderheadhalo"
             :class="headerhaloclassname" v-show="headhaloshow">
      </div>
      <div class="dialogcomwithborder-content display-grid" :class="contentclassname" >
        <slot></slot>
      </div>
    </div>
  </vue-draggable-resizable-gorkys>

</template>

<script>
  import basemixins from '@/common/buss/BaseMixins'

  export default {
    name: "dialogcomwithborder",
    props:
      {
        showheader: {
          type: String,
          default: "visible"
        },
          draggable:{
        	default:true,
          },
          // draghandle:{
			//   type: String,
			//   default: ""
          // },
        classname: String,
        subclassname: String,
        headerhaloclassname: String,
        contentclassname: String,
        headerhaloimgsrc: {
          type: String,
          default: "images/common/halo_232X2.png"
        },
        width: {
          type: Number,
          default: 300
        },
        height: {
          type: Number,
          default: 300
        },
        left: {
          type: Number,
          default: 0
        },
        top: {
          type: Number,
          default: 200
        },
        resizable: {
          type: Boolean,
          default: false
        },
        title: {
          type: String,
          required: true
        },
        oncloseCallback: {
          closefunc: Function
        },
        headhaloshow: {
          type: Boolean,
          default: true
        },
        headertitlestyle: {
          type: String,
          default: ""
        },
        closebtnshow: {
          type: Boolean,
          default: true
        },
        onResizeCallback: {
          resizefunc: Function
        }
      },
    computed: {
      headervisible() {
        return this.showheader == "visible";
      },
        // draghandlecom(){
		// 	return this.headervisible?'dialogcomwithborder-header':this.draghandle;
        // }
    },
    mixins: [basemixins.COMPONENTS_DRAGGABLE_RESIZABLE_GORKYS],
    methods: {
      close() {
        this.oncloseCallback();
      },
      onResize() {
        if (this.onResizeCallback) {
          this.onResizeCallback();
        }
      },
    }
  }
</script>

<style scoped>

</style>
