Cesium.Color.prototype.toRGBNum = function () {
    return parseInt(((this.red * 255) << 16) + ((this.green * 255) << 8) + this.blue * 255);
}
Cesium.Color.fromRGBNum = function (colornum) {
    let red = colornum >> 16;
    let green = (colornum >> 8) % 256;
    let blue = colornum % 256;
    return new Cesium.Color(red / 255, green / 255, blue / 255);
}

