    //获取绘制句柄
    getDarwHandler(viewer, drawMode, clampMode = 0) {
        if (this.drawHandler) {
            this.drawHandler.clear();
            this.drawHandler.deactivate();
        }
        this.drawHandler = new Cesium.DrawHandler(this.viewer, drawMode, clampMode);
        return this.drawHandler;
    }

