    //获取当前场景的标准相机(基于标准椭球体)
    getCamera() {
        let ellipsoid = this.viewer.scene.globe.ellipsoid;
        let cartesian3 = this.viewer.camera.positionWC;
        let cartographic = ellipsoid.cartesianToCartographic(cartesian3);
        let lat = Cesium.Math.toDegrees(cartographic.latitude);
        let lng = Cesium.Math.toDegrees(cartographic.longitude);
        let alt = cartographic.height;
        return {
            longitude: lng,
            latitude: lat,
            height: alt,
            heading: Cesium.Math.toDegrees(this.viewer.camera.heading),
            pitch: Cesium.Math.toDegrees(this.viewer.camera.pitch),
            roll: 0/*Cesium.Math.toDegrees(this.viewer.camera.roll)*/
        }
    }

    //获取相机到local坐标系的转换矩阵（local坐标系以当前相机为原点）
    getCameraToLocalTransMatrix(){
        let viewer = this.viewer;
        let camera = viewer.scene.camera;
        let cameraright = camera.right;
        let cameraup = camera.up;
        let fancameradir = new Cesium.Cartesian3();
        Cesium.Cartesian3.negate(camera.direction, fancameradir);

        let vmatrix = new Cesium.Matrix3(
            cameraright.x, cameraup.x, fancameradir.x,
            cameraright.y, cameraup.y, fancameradir.y,
            cameraright.z, cameraup.z, fancameradir.z,
        );

        let cartesian0 = Cesium.Cartesian3.clone(viewer.camera.position);
        let localunitx = new coordinates.CoordinateLocal(1, 0, 0);
        let localunity = new coordinates.CoordinateLocal(0, 1, 0);
        let localunitz = new coordinates.CoordinateLocal(0, 0, 1);
        let cartesianx = coordinates.CoordinateLocal.ToCartesian(localunitx, cartesian0);
        let cartesiany = coordinates.CoordinateLocal.ToCartesian(localunity, cartesian0);
        let cartesianz = coordinates.CoordinateLocal.ToCartesian(localunitz, cartesian0);
        let bendiji1 = new Cesium.Cartesian3(cartesianx.x - cartesian0.x, cartesianx.y - cartesian0.y, cartesianx.z - cartesian0.z);
        let bendiji2 = new Cesium.Cartesian3(cartesiany.x - cartesian0.x, cartesiany.y - cartesian0.y, cartesiany.z - cartesian0.z);
        let bendiji3 = new Cesium.Cartesian3(cartesianz.x - cartesian0.x, cartesianz.y - cartesian0.y, cartesianz.z - cartesian0.z);
        let umatrix = new Cesium.Matrix3(
            bendiji1.x, bendiji2.x, bendiji3.x,
            bendiji1.y, bendiji2.y, bendiji3.y,
            bendiji1.z, bendiji2.z, bendiji3.z
        )
        let umatrixinverse = new Cesium.Matrix3();
        Cesium.Matrix3.inverse(umatrix, umatrixinverse);


        let transformmatrix = new Cesium.Matrix3();
        Cesium.Matrix3.multiply(umatrixinverse, vmatrix, transformmatrix);

        return transformmatrix;
    }



    //获取射线和地球的交点/如果不相交 返回交点的三维空间坐标（射线必须是三维世界坐标）
    getIntersectPoint() {
        let position = this.viewer.camera.position;
        let direction = this.viewer.camera.direction;
        let ray = new Cesium.Ray(position, direction);
        let rayPoint = this.viewer.scene.globe.pick(ray, this.viewer.scene);
        if (Cesium.defined(rayPoint)) {
            let cartographic = Cesium.Ellipsoid.WGS84.cartesianToCartographic(rayPoint);
            return {
                Latitude: Cesium.Math.toDegrees(cartographic.latitude),
                Longitude: Cesium.Math.toDegrees(cartographic.longitude),
                Altitude: cartographic.height
            }
        } else
            return null;
    }

