    GetPolylineMidPoint(wgs84poilist) {
        let dis = coordinates.CoordinateWGS84.GetDistancePlaneByList(wgs84poilist) / 2;
        let curdis = 0, i = 1;
        for (; i < wgs84poilist.length && curdis < dis; i++) {
            curdis += coordinates.CoordinateWGS84.GetDistancePlane(wgs84poilist[i - 1], wgs84poilist[i]);
        }
        if (i >= wgs84poilist.length - 1) {
            i -= 1;
        }
        let lastdis = coordinates.CoordinateWGS84.GetDistancePlane(wgs84poilist[i - 1], wgs84poilist[i]);
        curdis -= lastdis;
        let ratio = (dis - curdis) / lastdis;
        return new coordinates.CoordinateWGS84((wgs84poilist[i].longitude - wgs84poilist[i - 1].longitude) * ratio + wgs84poilist[i - 1].longitude,
            (wgs84poilist[i].latitude - wgs84poilist[i - 1].latitude) * ratio + wgs84poilist[i - 1].latitude,
            (wgs84poilist[i].height - wgs84poilist[i - 1].height) * ratio + wgs84poilist[i - 1].height);
    }

