<template>
    <div>
        <dialogcomwithborder id="VisualAnalysis" title="通视性分析"
                             :width="352" :height="124" :left="left" :top="100"
                             headertitlestyle="left:20px;transform:translate(0px,0px);line-height:40px;font-size:16px;font-weight:bold;"
                             v-show="isShow" :headhaloshow="false" :onclose-callback="close">
            <template v-slot:title>
                <img src="images/cesium/SpaceAnalysis/VisualAnalysis.svg" class="float-left" style="width:20px;height:20px;margin:10px 8px 10px 0px;">
                <div class="float-left">通视性分析</div>
            </template>
            <div id="Content">
                <el-button :class="curflag==1?'float-left select':'float-left'" style="margin-right:30px;width:100px;" @click="addViewPoint">选取观察位置</el-button>
                <el-button :class="curflag==2?'float-left select':'float-left'" style="margin-right:30px;width:100px;" @click="addTargetPoint">添加目标点</el-button>
                <el-button style="width:30px;" class="spatialanalysisclear float-left" @click="clearVisualAnalysis"></el-button>
            </div>
        </dialogcomwithborder>
    </div>

</template>

<script>
	import dialogcomwithborder from '@/common/components/dialogcomwithborder'
	import basemixins from '@/common/buss/BaseMixins'
    import visualanalysishandler from '../buss/visualanalysis'

    import Bus from '@/common/buss/Bus'

	export default {
		name: "VisualAnalysis",
		components:{dialogcomwithborder},
		mixins: [basemixins.DATA_ISSHOW],
        props:['winWidth'],
		data() {
			return {

				curflag:-1,
                startdrawvisibilitypois:false,
                drawpoilength:0
            };
		},
		computed: {

			left: function () {
				return (this.winWidth - 34 - 20 - 352 - 22);
			}
        },
		mounted() {


            let that = this;
            Bus.VM.$on("Scene_Mouse_Left_Click", function (movement) {
                if (!that.startdrawvisibilitypois) return;
                that.drawpoilength++;
                visualanalysishandler.HandleLeftClick(movement.position, that.curflag);
            });

		},
		methods: {
			close(){
				this.isShow=false;
				this.$emit('onclose');
            },
            clearVisualAnalysis(){
				this.curflag=-1;
				visualanalysishandler.HandleClearAnalysis();
                this.startdrawvisibilitypois=false;
            },
			addViewPoint(){
                this.curflag = 1;
				this.startdrawvisibilitypois = true;
            },
            addTargetPoint(){
                this.curflag = 2;
                //visualanalysishandler.addTargetPoint();
            }
        },
		watch: {
			isShow(newval,oldval){
				if(this.isShow==false){
					this.clearVisualAnalysis();
                }
                else{
                	//visualanalysishandler.init();
                }
            }
        }
	}
</script>

<style scoped>

</style>

<style>
    #VisualAnalysis #Content{
        width:290px;
        height:30px;
        margin:auto;
    }
    #VisualAnalysis #Content button{
        height:30px;
        line-height: 30px;
        padding:0px;
        border-radius: 2px;
        border:none;
        background-color: #049effe5;
        color:white;
        box-sizing: border-box;
        margin-left:0px;
    }
    #VisualAnalysis #Content button.select {
        background-color: #049eff;
    }
</style>
