<template>
    <div>
        <dialogcomwithborder id="SkyLineCom" title="天际线分析" class="position-fixed"
                             :width="352" :height="showdrawchart?350:124" :left="left" :top="100"
                             headertitlestyle="left:20px;transform:translate(0px,0px);line-height:40px;font-size:16px;font-weight:bold;"
                             v-show="isShow" :headhaloshow="false" :onclose-callback="close">
            <template v-slot:title>
                <img src="images/cesium/SpaceAnalysis/SkylineAnalysis.svg"
                     class="float-left" style="width:20px;height:20px;margin:10px 8px 10px 0px;">
                <div class="float-left">天际线分析</div>
            </template>
            <div id="Content" :class="showdrawchart?'select':''">
                <div style="height:30px;" class="width100percent">
                    <el-button class="float-left" style="margin-right:30px;width:100px;" @click="clickextractskyline">提取天际线</el-button>
                    <el-button :class="showdrawchart?'float-left select':'float-left'" style="margin-right:30px;width:100px;" @click="draw2dskyline">绘制天际线</el-button>
                    <el-button  style="width:30px;" class="spatialanalysisclear float-left" @click="clearSkyLine"></el-button>
                </div>
                <div class="skyline-div" id="skyline-map"></div>
            </div>
        </dialogcomwithborder>
    </div>

</template>

<script>
	import dialogcomwithborder from '@/common/components/dialogcomwithborder'
	import basemixins from '@/common/buss/BaseMixins'
	import skyLineHandle from '../buss/skyLineHandle'



	export default {
		name: "SkyLineRraw",
		components: {dialogcomwithborder},
		mixins: [basemixins.DATA_ISSHOW],
		created() {
			// skyLineHandle.SetVueInstance(this);
		},

        props:['winWidth'],

		data() {
			return {


				skyLineEnable: false,
				generateSkyLineEnable: false,
				data: [],
				showdrawchart: false,
			}
		},
        computed:{

			left:function () {
                return (this.winWidth - 34 - 20 - 352 - 22);
			}
        },
        mounted() {

        },
		methods: {
			close() {
				this.isShow = false;
				this.$emit('onclose');
			},
			clickextractskyline() {
				this.showdrawchart = false;
				skyLineHandle.clearSkyLine();
				skyLineHandle.extractSkyline();
			},

            draw2dskyline(){
                if(skyLineHandle.checkskyline2d()==true){
					this.showdrawchart = true;
					skyLineHandle.getSkyline2D();
                }
            },
			clearSkyLine() {
				this.showdrawchart=false;
				skyLineHandle.clearSkyLine();
			},

		},
		watch: {
			isShow(newval, oldval) {
				if (newval == false) {
					this.clearSkyLine();
				}
			},
		}
	}
</script>

<style scoped>

</style>

<style>
    #SkyLineCom #Content {
        width:290px;
        height: 30px;
        margin: auto;
        overflow: hidden;
    }
    #SkyLineCom #Content.select {
        height: 256px;
        margin: auto;
    }



    #SkyLineCom #Content button {
        height: 30px;
        line-height: 30px;
        padding: 0px;
        border-radius: 2px;
        border: none;
        background-color: #049effb2;
        color: white;
        box-sizing: border-box;
        margin-left: 0px;
    }
    #SkyLineCom #Content button.select{
        background-color: #049eff;
    }
    #SkyLineCom #Content .skyline-div{
        width: 100%;
        height:200px;
        margin-top:26px;
    }

</style>
