<template>
    <div>
        <dialogcomwithborder id="mapprint" title="地图打印" style="z-index: 99;"
                             :width="302" :height="434" :left="100" :top="50"
                             headertitlestyle="left:20px;transform:translate(0px,0px);line-height:40px;font-size:16px;font-weight:bold;"
                             v-show="isShow" :headhaloshow="false" :onclose-callback="closeAll">
            <el-row style="height:30px;">
                <el-col :span="6" class="mapprintlabel" style="font-size: 15px;">标题：</el-col>
                <el-col :span="18">
                    <!--<el-select></el-select>-->
                    <el-input  v-model="title"></el-input>
                </el-col>
            </el-row>
            <el-row style="height:96px;">
                <el-col :span="6" class="mapprintlabel">范围：</el-col>
                <el-col :span="18" style="padding-top: 4px;">
                    <el-row class="mapprintlabel margin-left8px" style="text-align: left">
                        <el-col :span="6">minX：</el-col>
                        <el-col :span="18" >{{west.toFixed(8)}}</el-col>
                    </el-row>
                    <el-row class="mapprintlabel margin-left8px" style="text-align: left">
                        <el-col :span="6">maxX：</el-col>
                        <el-col :span="18" >{{east.toFixed(8)}}</el-col>
                    </el-row>
                    <el-row class="mapprintlabel margin-left8px" style="text-align: left">
                        <el-col :span="6">minY：</el-col>
                        <el-col :span="18" >{{south.toFixed(8)}}</el-col>
                    </el-row>
                    <el-row class="mapprintlabel margin-left8px" style="text-align: left">
                        <el-col :span="6">maxY：</el-col>
                        <el-col :span="18" >{{north.toFixed(8)}}</el-col>
                    </el-row>
                </el-col>
            </el-row>
            <el-row style="height:30px;place-self: center;margin-left:20px;" class="width70percent">
                <el-button  class="float-left" style="width:6em;" @click="rangetype=2">框选范围</el-button>
                <el-button  class="float-right" style="width:6em;" @click="rangetype=1">当前地图</el-button>
            </el-row>
            <el-row style="height:110px;" class="MapPrintSettings">
                <el-col :span="6" class="mapprintlabel">设置：</el-col>
                <el-col :span="18" style="padding-top: 4px;padding-right:30px;">
                    <el-row class="mapprintlabel">
                        <el-col :span="12">是否有标题</el-col>
                        <el-col :span="12">
<!--                            <el-switch-->
<!--                                       class="float-right margin-top2px" :width="20" v-model="titleFlag"-->
<!--                                       active-color="#049eff"-->
<!--                                       inactive-color="#7d7d7d"></el-switch>-->
                            <el-switch v-model="titleFlag" style="margin-top:-4px;"/>
                        </el-col>
                    </el-row>
                    <el-row class="mapprintlabel margin-top10px">
                        <el-col :span="10">导出模板</el-col>
                        <el-col :span="14">
                            <el-select  style="width: 60px;" placeholder="模板" v-model="exportModel">
                                <el-option
                                        key="0"
                                        label="横版"
                                        value="0">
                                </el-option>
                                <el-option
                                        key="1"
                                        label="竖版"
                                        value="1">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                    <el-row class="mapprintlabel margin-top10px">
                        <el-col :span="10">导出格式</el-col>
                        <el-col :span="14">
                            <el-select style="width: 60px;" placeholder="png" v-model="exportFormat">
                                <el-option
                                        key="0"
                                        label="png"
                                        value="png">
                                </el-option>
                                <el-option
                                        key="1"
                                        label="jpeg"
                                        value="jpeg">
                                </el-option>
                            </el-select>
                        </el-col>
                    </el-row>
                </el-col>
            </el-row>
            <el-row style="height:30px;place-self: center;width:60%;">
                <el-button  class="float-left" style="width:4em;" @click="onPreview">预览</el-button>
                <el-button  class="float-right" style="width:4em;" @click="onPrint">打印</el-button>
            </el-row>
        </dialogcomwithborder>
        <dialogcomwithborder title="预览" style="z-index: 100;" id="mapprintDialog"
                             :width="dialogwidth" :height="dialogheight" :left="0" :top="0"
                             classname="mapPrintDiag"
                             headertitlestyle="left:20px;transform:translate(0px,0px);line-height:40px;font-size:16px;font-weight:bold;"
                             v-show="previewDigShw" :headhaloshow="false" :onclose-callback="closeDiag">
            <img class="width100percent height100percent" :src="previewimageUrl">
        </dialogcomwithborder>



        <div ref="ClipRect" id="MapPrintClipRect" style="border:1px solid yellow;position:fixed;"></div>

    </div>

</template>

<script>
	import Bus from '@/common/buss/Bus'
	import basemixins from '@/common/buss/BaseMixins'
	import dialogcomwithborder from '@/common/components/dialogcomwithborder'
	import cesiumUtil from '@/modules/cesium/common/buss/CesiumViewer'
	import msgUtil from '@/common/util/MessageUtil'
	import coordinate from '@/common/util/Coordinates'



    const ENTITYPRE='MAPPRINT'

	let vueInstance = null;
	export default {
		name: "mapprint",
		data() {
			return {
				menucontentcurwidth: null,
				west: 0,
				east: 0,
				north: 0,
				south: 0,
				startX: 0,
				startY: 0,
				rectWidth: 0,
				rectHeight: 0,
				titleFlag: true,
				exportModel: "0",
				exportFormat: 'png',
				title: null,
				previewDigShw: false,
				previewimageUrl: null,
				legendList: [],
				legendCanvasEnlarge: 1, //图例画布放大倍数,
				legendCanvasWidth: 0,
				legendCanvasHeight: 0,
				dialogwidth: 1822,
				dialogheight: 916,
				rangetype: 1,//1:当前地图 2：地图框选
				leftdown: false,//是否按下鼠标左键
				defaultStrokeWidth: 1,
				defaultStrokeColor: 'yellow',
			};
		},
		mixins: [basemixins.DATA_ISSHOW],
		components: {dialogcomwithborder},
		computed: {

			boundsettings: function () {
				let result = [
					{name: 'minX', val: this.west},
					{name: 'maxX', val: this.east},
					{name: 'minY', val: this.south},
					{name: 'maxY', val: this.north},
				]
				return result;
			}
		},
		mounted() {
			Bus.VM.$on(Bus.SignalType.Scene_Camera_Changed, this.showLocation);
			vueInstance = this;
		},
		beforeDestroy() {
			Bus.VM.$off(Bus.SignalType.Scene_Camera_Changed, this.showLocation);
		},
		methods: {
			showLocation() {
				let rectangle = new Cesium.Rectangle();
				if (this.rangetype === 1) {
					cesiumUtil.CesiumViewer.getInstance().GetViewerByContainerid().camera.computeViewRectangle(Cesium.Ellipsoid.WGS84, rectangle);
				}
				else {
					rectangle = this.getframerectangle();
				}
				this.west = Cesium.Math.toDegrees(rectangle.west);
				this.north = Cesium.Math.toDegrees(rectangle.north);
				this.east = Cesium.Math.toDegrees(rectangle.east);
				this.south = Cesium.Math.toDegrees(rectangle.south);
			},
			getframerectangle() {
				if (this.startX < 0 || this.startY < 0 || this.startY < 0 || this.startY < 0) {
					return Cesium.Rectangle.fromDegrees();
				}
				let leftbottomwgs84pos = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(new Cesium.Cartesian2(this.startX, this.startY + this.rectHeight));
				let lefttopwgs84pos = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(new Cesium.Cartesian2(this.startX, this.startY));
				let rightbottomwgspos = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(new Cesium.Cartesian2(this.startX + this.rectWidth, this.startY + this.rectHeight));
				let righttopwgs84pos = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(new Cesium.Cartesian2(this.startX + this.rectWidth, this.startY));
				if (!leftbottomwgs84pos || !rightbottomwgspos) {
					return Cesium.Rectangle.fromDegrees();
				}
				else if (!lefttopwgs84pos || !righttopwgs84pos) {
					let bottom = this.startY + this.rectHeight, top = (this.startY * 2 + this.rectHeight) / 2;
					if (!lefttopwgs84pos) {
						//左上角没有则二分法查找左上角
						let lastlefttopwgs84pos = new coordinate.CoordinateWGS84(leftbottomwgs84pos.longitude, leftbottomwgs84pos.latitude, leftbottomwgs84pos.height);
						let newlefttopwgs84pos = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(new Cesium.Cartesian2(this.startX, top));
						while (bottom > top) {
							if (!newlefttopwgs84pos) {
								top = (top + bottom) / 2;
								newlefttopwgs84pos = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(new Cesium.Cartesian2(this.startX, top));
							}
							else {
								bottom = (top + bottom) / 2;
								top = (top + bottom) / 2;
								lastlefttopwgs84pos = new coordinate.CoordinateWGS84(lastlefttopwgs84pos.longitude, lastlefttopwgs84pos.latitude, lastlefttopwgs84pos.height);
								newlefttopwgs84pos = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(new Cesium.Cartesian2(this.startX, top));
							}
						}
						if (!newlefttopwgs84pos) {
							newlefttopwgs84pos = lastlefttopwgs84pos;
						}
						lefttopwgs84pos = new coordinate.CoordinateWGS84(newlefttopwgs84pos.longitude, newlefttopwgs84pos.latitude, newlefttopwgs84pos.height);
					}
					if (!righttopwgs84pos) {
						//右上角没有则二分法查找右上角
						let lastrighttopwgs84pos = new coordinate.CoordinateWGS84(rightbottomwgspos.longitude, rightbottomwgspos.latitude, rightbottomwgspos.height);
						let newrighttopwgs84pos = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(new Cesium.Cartesian2(this.startX + this.rectWidth, top));
						while (bottom > top) {
							if (!newrighttopwgs84pos) {
								top = (top + bottom) / 2;
								newrighttopwgs84pos = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(new Cesium.Cartesian2(this.startX + this.rectWidth, top));
							}
							else {
								bottom = (top + bottom) / 2;
								top = (top + bottom) / 2;
								lastrighttopwgs84pos = new coordinate.CoordinateWGS84(newrighttopwgs84pos.longitude, newrighttopwgs84pos.latitude, newrighttopwgs84pos.height);
								newrighttopwgs84pos = cesiumUtil.CesiumViewer.getInstance().GetPickedRayPositionWGS84(new Cesium.Cartesian2(this.startX + this.rectWidth, top));
							}
						}
						if (!newrighttopwgs84pos) {
							newrighttopwgs84pos = lastrighttopwgs84pos;
						}
						righttopwgs84pos = new coordinate.CoordinateWGS84(newrighttopwgs84pos.longitude, newrighttopwgs84pos.latitude, newrighttopwgs84pos.height);
					}
				}
				return Cesium.Rectangle.fromDegrees(
					Math.min.apply(null, [lefttopwgs84pos.longitude, leftbottomwgs84pos.longitude, righttopwgs84pos.longitude, rightbottomwgspos.longitude]),
					Math.min.apply(null, [lefttopwgs84pos.latitude, leftbottomwgs84pos.latitude, righttopwgs84pos.latitude, rightbottomwgspos.latitude]),
					Math.max.apply(null, [lefttopwgs84pos.longitude, leftbottomwgs84pos.longitude, righttopwgs84pos.longitude, rightbottomwgspos.longitude]),
					Math.max.apply(null, [lefttopwgs84pos.latitude, leftbottomwgs84pos.latitude, righttopwgs84pos.latitude, rightbottomwgspos.latitude]));
			},
			onPreview() {
				this.previewDigShw = true;
				if (!this.validate()) {
					return;
				}
				let viewer = cesiumUtil.CesiumViewer.getInstance().viewer;
				let ratio = 1;
				if (this.rangetype === 1) {
					ratio = viewer.canvas.width / viewer.canvas.height;
					this.dialogheight = viewer.canvas.height;
					this.dialogwidth = parseInt((this.dialogheight - 40) * ratio);
				}
				else {
					if (this.rectHeight < 0) {
						this.startY += this.rectHeight;
						this.rectHeight = -this.rectHeight;
					}
					if (this.rectWidth < 0) {
						this.startX += this.rectWidth;
						this.rectWidth = -this.rectWidth;
					}
					ratio = this.rectWidth / this.rectHeight;
					this.dialogheight = this.rectHeight + 40;
					if (this.dialogheight > viewer.canvas.height) {
						//超出屏幕范围则设定为最大高度
						this.dialogheight = viewer.canvas.height;
					}
					this.dialogwidth = parseInt((this.dialogheight - 40) * ratio);
				}

				this.getImageUrl();
			},
			onPrint() {
				if (!this.validate()) {
					return;
				}
				let that = this;
				this.getImageUrl(() => {
					let a = document.createElement('a');
					let event = new MouseEvent('click');
					a.download = that.title ? that.title : '地图打印' + "." + that.exportFormat; // 指定下载图片的名称
					a.href = that.previewimageUrl;
					a.dispatchEvent(event); // 触发超链接的点击事件
				});
			},
            closeAll(){
				this.isShow=false;
				this.$emit("close");
            },
			closeDiag() {
				this.previewDigShw = false;
			},
			HiddenMenuWithAnamate(){
				this.closeAll();
            },
			ShowMenu(){
				this.isShow=true;
            },
			HiddenMenu(){
				this.isShow=false;
            },
			validate() {
				if (this.titleFlag && this.title && this.title.length > 15) {
					msgUtil.messagePrompt('error', "标题不能超过15个字！");
					return false;
				}
				return true;
			},
 			getImageUrl(call) {
                let that = this;

                this.previewimageUrl = '';
                let scene = cesiumUtil.CesiumViewer.getInstance().viewer.scene;
                let dataURL = scene.canvas.toDataURL("image/png");

                let image = new Image();
                image.src = dataURL;
                image.onload = function () {
                    let canvas = that.convertImageToCanvas(image);
                    if (that.titleFlag && that.title) {
                        that.addTitle(canvas);
                    }
                    that.previewimageUrl = canvas.toDataURL("image/" + that.exportFormat);
                    if(typeof call === "function") call();
                }
			},
			/**
			 * 根据图片生成画布
			 */
			convertImageToCanvas(image) {
				let canvas = document.createElement('canvas');
				/*canvas.width = $('#legendImg').width();
                canvas.height = $('#legendImg').height();*/
				if (this.rangetype === 1) {
					canvas.width = image.width;
					canvas.height = image.height;
					canvas.getContext("2d").drawImage(image, 0, 0);
				}
				else {
					canvas.width = this.rectWidth;
					canvas.height = this.rectHeight;
					//对图像进行裁剪
					canvas.getContext("2d").drawImage(image,
						this.startX, this.startY, this.rectWidth, this.rectHeight, 0, 0, this.rectWidth, this.rectHeight);
				}
				return canvas;
			},
			addTitle(canvas) {
				let ctx = canvas.getContext("2d");
				let w = ctx.canvas.width;
				let h = ctx.canvas.height;
				ctx.font = 'bold 2rem arial'; //#设置文字粗体 字号 行高 字体
				ctx.lineWidth = 6;
				if (this.exportModel === "0") {
					ctx.textAlign = "center";
					ctx.textBaseline = "top";
					// ctx.fillText(this.title, w / 2, h / 50);
					ctx.strokeStyle = "#FFF";
					ctx.strokeText(this.title, w / 2, h / 50);
					ctx.fillStyle = "#01a0a1";
					ctx.fillText(this.title, w / 2, h / 50);
				} else if (this.exportModel === "1") {
					ctx.textBaseline = 'middle'; //#垂直居中
					ctx.textAlign = 'center'; //#水平居中
					for (let b = 0; b < this.title.length; b++) { //#按行写入文本
						ctx.strokeStyle = "#FFF";
						ctx.strokeText(this.title[b], w / 50, h / 2 - (this.title.length * 40 / 2) + b * 40);
						ctx.fillStyle = "#01a0a1";
						ctx.fillText(this.title[b], w / 50, h / 2 - (this.title.length * 40 / 2) + b * 40);
					}
				}
			},
			onmousemove(movement) {

				// this.$refs.MouseMoveTip.style.left = (movement.pageX + 6).toString() + "px";
				// this.$refs.MouseMoveTip.style.top = (movement.pageY + 6).toString() + "px";
				// if(vueInstance.leftdown===false) return;
				// vueInstance.rectWidth = movement.position.x - vueInstance.startX - vueInstance.defaultStrokeWidth;
				// vueInstance.rectHeight = movement.position.y - vueInstance.startY - vueInstance.defaultStrokeWidth;
				// var cliprect = document.getElementById("MapPrintClipRect");
				// if (!cliprect) return;
				// cliprect.width=vueInstance.rectWidth;
				// cliprect.height=vueInstance.rectHeight;
				// cliprect.left=vueInstance.startX;
				// cliprect.top=vueInstance.startY;
			},
			onsceneleftdown(movement) {
				let viewer = cesiumUtil.CesiumViewer.getInstance().viewer;
				vueInstance.leftdown = true;
				vueInstance.startX = movement.position.x + vueInstance.defaultStrokeWidth;
				vueInstance.startY = movement.position.y + vueInstance.defaultStrokeWidth;
			},
			onsceneleftup(movement) {
				vueInstance.leftdown = false;
			},
			//开启框选模式
			openframeselect() {
				msgUtil.notifyPrompt("框选提示", "请在地图中拖拽框选", 'warning');
				//Bus.VM.$on(Bus.SignalType.AppMouseMove, this.onmousemove);
				// Bus.VM.$on(Bus.SignalType.Scene_Left_Down,this.onsceneleftdown);
				// Bus.VM.$on(Bus.SignalType.Scene_Left_Up,this.onsceneleftup);
				let viewer = cesiumUtil.CesiumViewer.getInstance().viewer;
				//创建全屏打印区域
				var canvas = document.createElement('canvas');
				canvas.id =ENTITYPRE + "canvas";
				// 网页可见区域宽设置
				canvas.width = viewer.canvas.width;
				// 网页可见区域高设置
				canvas.height = viewer.canvas.height;
				canvas.style = "position: fixed;background:#33000000;left: " + viewer.canvas.offsetLeft + ";top: " + viewer.canvas.offsetTop + ";";
				document.body.appendChild(canvas);
				//canvas 的矩形框
				var canvasRect = canvas.getBoundingClientRect();
				//矩形框的左上角坐标
				var canvasLeft = canvasRect.left;
				var canvasTop = canvasRect.top;
				var x = 0;
				var y = 0;
				var defaultStrokeWidth = 1;
				var penColor = 'yellow';
				let that = this;
				//鼠标点击按下事件，画图准备
				canvas.onmousedown = function (e) {
					that.leftdown = true;
					that.startX = e.clientX + defaultStrokeWidth;
					that.startY = e.clientY + defaultStrokeWidth;
					x = e.clientX - canvasLeft;
					y = e.clientY - canvasTop;
					//鼠标移动事件，画图
					canvas.onmousemove = function (e) {
						var canvas = document.getElementById(ENTITYPRE + "canvas");
						if (!canvas) return;
						that.rectWidth = e.clientX - that.startX - defaultStrokeWidth;
						that.rectHeight = e.clientY - that.startY - defaultStrokeWidth;
						var width = e.clientX - canvasLeft - x;
						var height = e.clientY - canvasTop - y;
						var context = canvas.getContext('2d');
						context.clearRect(0, 0, canvas.width, canvas.height);
						// 颜色设置，必须放在绘制之前
						context.strokeStyle = penColor;
						// 线宽设置，必须放在绘制之前
						context.lineWidth = defaultStrokeWidth;
						context.strokeRect(that.startX, that.startY, width, height);
						that.showLocation();
					}
				};
				//鼠标抬起
				canvas.onmouseup = function (e) {
					that.leftdown = false;
					var canvas = document.getElementById(ENTITYPRE + "canvas");
					if (!canvas) return;
					canvas.onmousemove = null;
					//删除画笔区域
					//document.body.removeChild(canvas);
				}
			},
			//关闭框选模式
			closeframeselect() {
				//Bus.VM.$off(Bus.SignalType.AppMouseMove, this.onmousemove);
				// Bus.VM.$off(Bus.SignalType.Scene_Left_Down,this.onsceneleftdown);
				// Bus.VM.$off(Bus.SignalType.Scene_Left_Up,this.onsceneleftup);
				var canvas = document.getElementById(ENTITYPRE + "canvas");
				if (!canvas) return;
				//删除画笔区域
				document.body.removeChild(canvas);
				this.startX = 0;
				this.startY = 0;
				this.rectWidth = 0;
				this.rectHeight = 0;

			}
		},
		watch: {
			isShow(newval, oldval) {
				if (newval === true) {
					this.showLocation();
				}
				else {
					this.rangetype = 1;
					this.previewDigShw=false;
				}
			},
			rangetype(newval, oldval) {
				if (newval === 2) {
					this.openframeselect();
				}
				else {
					this.closeframeselect();
					this.showLocation();
				}
			}
		}
	}
</script>

<style scoped>

</style>
<style>
    #mapprint .dialogcomwithborder-content {
        padding: 16px 30px 20px 12px;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
    }

    #mapprint .dialogcomwithborder-content .el-row {
        margin-bottom: 12px;
    }

    #mapprint .dialogcomwithborder-content .el-row .el-col .el-row {
        margin-bottom: 0px;
        height: 24px;
        line-height: 24px;
    }

    #mapprint .dialogcomwithborder-content .el-row .mapprintlabel {
        color: #ffffffb2;
        height: 30px;
        line-height: 30px;
        text-align: right;
        vertical-align: middle;
        font-size: 13px;
        padding: 0 12px 0 0;
        box-sizing: border-box;
    }

    #mapprint .dialogcomwithborder-content .el-input__inner {
        background: transparent;
        height: 30px;
        line-height: 30px;
        font-size: 12px;
        color: #ffffffb2;
        padding: 0 8px;
    }

    #mapprint .el-button {
        padding: 0;
        border-radius: 3px;
        color: white;
        border: none;
        background: #049effb2;
        height: 26px;
        font-size: 13px;

    }

    /*#mapprint .el-switch__core {*/
