<template>
    <div id="SpatialTools">
        <div id="MainFrame" class="position-fixed">
            <div :class="curTool&&curTool.name=='地图打印'?'funcbt select':'funcbt'" @click="clickmenu('地图打印')">地图打印</div>
            <div>|</div>
            <div :class="curTool&&curTool.name=='空间分析'?'funcbt select':'funcbt'" @click="clickmenu('空间分析')">空间分析</div>
            <div>|</div>
            <div :class="curTool&&curTool.name=='空间量算'?'funcbt select':'funcbt'" @click="clickmenu('空间量算')">空间量算</div>
            <div>|</div>
            <div :class="curTool&&curTool.name=='三维特效'?'funcbt select':'funcbt'" @click="clickmenu('三维特效')">三维特效</div>
            <div>|</div>
            <div style="padding:0px;" :class="curTool&&curTool.name=='官网'?'funcbt select':'funcbt'" @click="clickmenu('官网')">官网</div>
        </div>
        <spaceanalysistool  ref="spaceanalysistool" @close="onclosetool('空间分析')" :winWidth="winWidth"></spaceanalysistool>
        <spacemeasuretool  ref="spacemeasuretool" @close="onclosetool('空间量算')" :winWidth="winWidth"></spacemeasuretool>
        <specialeffectstool  ref="specialeffectstool" @close="onclosetool('三维特效')" :winWidth="winWidth"></specialeffectstool>
        <mapprinttool ref="mapprinttool" @close="onclosetool('地图打印')"></mapprinttool>

    </div>
</template>

<script>
    import spaceanalysistool from './SpaceAnalysis/common/components/SpatialCommon'
    import spacemeasuretool from './SpaceMeasure/common/components/SpaceMeasure'
    import specialeffectstool from './SpecialEffects/common/components/SpecialEffects'
    import mapprinttool from './MapPrint/components/mapprint'


	export default {
		name: "SpatialTools",
		data() {
			return {
				curTool: null,
                SubTools:[
                    {name:"地图打印",refname:"mapprinttool"},
					{name:"空间分析",refname:"spaceanalysistool"},
					{name:"空间量算",refname:"spacemeasuretool"},
					{name:"三维特效",refname:"specialeffectstool"},
					{name:"官网",refname:""},
                ]
            };
		},
		props:['winWidth'],
        components:{
		    spaceanalysistool,
            specialeffectstool,
            spacemeasuretool,
            mapprinttool
        },
		computed: {

        },
		mounted() {

		},
		methods: {
			clickmenu(menuname){
				let tool=this.SubTools.find(item=>item.name==menuname);
				let refname="";
				if(!tool) return;
				if(this.curTool&&menuname==this.curTool.name){
					//用动画效果关闭当前tool
					refname=this.curTool.refname;
					if(refname&&refname!=""&&this.$refs[refname].ShowMenu&&typeof this.$refs[refname].HiddenMenuWithAnamate=="function"){
						this.$refs[refname].HiddenMenuWithAnamate();
					}
					this.curTool=null;
                }
                else{
					this.closesubmenus();
					this.curTool=this.SubTools.find(item=>item.name==menuname);
					refname=this.curTool.refname;
					if(refname&&refname!=""&&this.$refs[refname].ShowMenu&&typeof this.$refs[refname].ShowMenu=="function"){
						this.$refs[refname].ShowMenu();
					}
                }
			},

            closesubmenus(){
				let that=this;
				this.SubTools.forEach(subtool=>{
					if(subtool.refname&&subtool.refname!=""&&that.$refs[subtool.refname].HiddenMenu&&typeof that.$refs[subtool.refname].HiddenMenu=="function"){
						that.$refs[subtool.refname].HiddenMenu();
                    }
                })
				this.curTool=null;
            },


            onclosetool(toolname){
				if(this.curTool&&this.curTool.name===toolname){
					this.curTool=null;
				}
            }

        },
		watch: {}
	}
</script>

<style scoped>

</style>

<style>
    #SpatialTools #MainFrame {
        background: #091426b2;
        height: 38px;
        color: #ffffffb2;
        line-height: 22px;
        top: 40px;
        right: 22px;
        padding: 8px;
        box-sizing: border-box;
    }

    #SpatialTools #MainFrame div {
        padding: 0 8px;
        float: left;
    }

    #SpatialTools #MainFrame .funcbt:hover {
        color: #22e8e27f;
    }

    #SpatialTools #MainFrame .select {
        color: #22e8e2;
    }
</style>
