<template>
    <div>
        <dialogcomwithborder id="LimitHeightCom" title="限高分析"
                             :width="400" :height="124" :left="left" :top="100"
                             headertitlestyle="left:20px;transform:translate(0px,0px);line-height:40px;font-size:16px;font-weight:bold;"
                             v-show="isShow" :headhaloshow="false" :onclose-callback="close">
            <template v-slot:title>
                <img src="images/cesium/SpaceAnalysis/HeightLimitAnalysis.svg" class="float-left" style="width:20px;height:20px;margin:10px 8px 10px 0px;">
                <div class="float-left">限高分析</div>
            </template>
            <div id="LimitHeightCom_Content">
                <el-button  class="float-left" :class="limitHeightEnable?'select':''" style="width:100px;" @click="limitHeightAnalysis">绘制范围</el-button>
                <el-button  style="width:30px;margin:0px 30px;" class="spatialanalysisclear float-left" @click="clearLimitHeight"></el-button>
                <div class="float-left FontSize-13px"  style="line-height: 30px;">限高阈值</div>
                <el-input class="float-left" v-model="linearExtrudeHeight"></el-input>
                <div class="float-left FontSize-13px" style="line-height: 30px;margin-left:8px;">米</div>
            </div>
        </dialogcomwithborder>
    </div>
    <!--<div class="SubContainer_ThreeButtonWithHalo float-left">-->
    <!--<div class="MenuSubLabel color-white FontSize-16px textalign-left">限高分析</div>-->
    <!--<div class="float-left margin-top10px" style="width: 33%">-->
    <!--<input type="image" class="horizon-center position-relative MenuSubicon"-->
    <!--:src="limitHeightEnable?require('../../../../../public/images/modules/SpaceAnalysis/linearExtrude_selected.svg'):require('../../../../../public/images/modules/SpaceAnalysis/linearExtrude.svg')" @click="limitHeightAnalysis"/>-->
    <!--<div :class="limitHeightEnable?'color-ffcc00 FontSize-14px textalign-center':'color-white FontSize-14px textalign-center'">分析</div>-->
    <!--</div>-->
    <!--<div class="float-left margin-top10px" style="width: 33%">-->
    <!--<input type="image" class="horizon-center position-relative MenuSubicon"-->
    <!--:src="require('../../../../../public/images/modules/SpaceAnalysis/cleargraphic.svg')" @click="clearLimitHeight"/>-->
    <!--<div class="color-white FontSize-14px textalign-center">清除</div>-->
    <!--</div>-->
    <!--<div class="float-left color-white" style="width: 33%;padding-left: 20px">-->
    <!--<span>限高阈值</span>-->
    <!--<input type="text" class="linear-extrude-setting-input" v-model="linearExtrudeHeight"/>&nbsp;<span>米</span>-->
    <!--</div>-->
    <!--</div>-->
</template>

<script>
	import dialogcomwithborder from '@/common/components/dialogcomwithborder'
	import basemixins from '@/common/buss/BaseMixins'
	import limitHandle from '../buss/limitHeightHandle'
	import msgUtil from '@/common/util/MessageUtil'


	export default {
		name: "LimitHeightCom",
		components: {dialogcomwithborder},
		mixins: [basemixins.DATA_ISSHOW],
        props: ['winWidth'],
		data() {
			return {





				limitHeightEnable: false,
				linearExtrudeHeight: 10, //限高阈值
			}
		},

        computed:{

            left:function () {
                return (this.winWidth - 34 - 20 - 400 - 22);
            }
        },
        mounted() {

        },
		created() {
			limitHandle.SetVueInstance(this);
		},
		methods: {
			limitHeightAnalysis() {
				//执行分析前判断阈值合法性
				if (this.judgeThreshold(this.linearExtrudeHeight)) {
					this.limitHeightEnable = !this.limitHeightEnable;
					limitHandle.linearExtrudeAna(this.linearExtrudeHeight);
				}
			},
			clearLimitHeight() {
				this.limitHeightEnable = false;
				limitHandle.clearLinearExtrude();
			},
			checkNum(value) {
				return !isNaN(value);
			},
			judgeThreshold(value) {
				if (!value) {
					msgUtil.messagePrompt('warning', '请设置限高阈值');
					return false;
				} else if (!this.checkNum(Number(value))) {
					msgUtil.messagePrompt('warning', '限高阈值格式非法');
					return false;
				} else if (Number(value) <= 0) {
					msgUtil.messagePrompt('warning', '限高阈值必须大于零');
					return false;
				} else {
					return true;
				}
			},
			close(){
				this.isShow=false;
                this.$emit('onclose');
			},
		},
		watch: {
			isShow(newval,oldval){
				if(newval==false){
					this.clearLimitHeight();
				}
			},
			limitHeightEnable(newval, oldval) {
				if (newval)
					this.$emit("clearOtherAnalysis");
			}
		}
	}
</script>

<style scoped>
    .linear-extrude-setting-input {
        width: 60%;
        border: 1px solid white;
        border-radius: 0.3vw;
        margin-top: 10px;
        background-color: transparent;
        color: white;
    }
</style>
<style>
    #LimitHeightCom #LimitHeightCom_Content{
        width:334px;
        height:30px;
        margin:auto;
        color:#ffffffb2;
    }
    #LimitHeightCom #LimitHeightCom_Content button{
        height:30px;
        line-height: 30px;
        padding:0px;
        border-radius: 2px;
        border:none;
        background-color: #049effe5;
        color:white;
        box-sizing: border-box;
        margin-left:0px;
    }

    #LimitHeightCom #LimitHeightCom_Content button.select {
        background-color: #049eff;
    }

    #LimitHeightCom #LimitHeightCom_Content .el-input{
        width: auto;
        height:auto;
        margin-left:8px;
    }
    #LimitHeightCom #LimitHeightCom_Content .el-input__inner{
        background: transparent;
        width: 60px;
        height: 30px;
        line-height: 30px;
        padding: 0 4px;
        color: #ffffffb2;
    }
</style>
