<template>
    <div id="SpatialCommon">
        <div id="SubMainFrame" class="position-fixed" v-show="false">
            <img v-for="(item,index) in subtools"
                                         :src="subcurTool&&subcurTool.name==item.name?'images/cesium/SpaceAnalysis/'+item.imgfilename+'_select.svg':'images/cesium/SpaceAnalysis/'+item.imgfilename+'.svg'"
                                         :title="item.name"
                 @click="clicksubmenu(item)">
            <div style="width:20px;height:1px;background:#ffffffb2;margin:auto;margin-top:9px;"></div>
            <img :src="'images/common/close.svg'" style="width:12px;height:12px;margin:9px 11px;" @click="HiddenMenuWithAnamate"/>
        </div>

        <sky-line ref="skyLine" @onclose="clearanasubmenus" :winWidth="winWidth"></sky-line>
        <sectionanalysis ref="sectionanalysis" @onclose="clearanasubmenus" :winWidth="winWidth"></sectionanalysis>
        <limit-height ref="limitHeight" @onclose="clearanasubmenus" :winWidth="winWidth"></limit-height>
        <visual-field ref="visualField" @onclose="clearanasubmenus" :winWidth="winWidth"></visual-field>
        <visual-analysis ref="visualAnalysis" @onclose="clearanasubmenus" :winWidth="winWidth"></visual-analysis>
        <sun-light ref="sunLight" @onclose="clearanasubmenus" :winWidth="winWidth"></sun-light>
        <cutfillcom ref="cutfillcom" @onclose="clearanasubmenus" :winWidth="winWidth"></cutfillcom>

    </div>
</template>

<script>
	import basemixins from '@/common/buss/BaseMixins'
	import skyLine from '../../SkyLineAnalysis/components/SkyLineCom'
	import sectionanalysis from '../../SectionAnalysis/components/SectionCom'
	import limitHeight from '../../limitHeightAnalysis/components/LimitHeightCom'
	import visualField from '../../visualFieldAnalysis/components/VisualField'
    import visualAnalysis from '../../VisualAnalysis/components/VisualAnalysis'
	import sunLight from '../../sunLightAnalysis/components/SunLightCom'
    import cutfillcom from '../../cutFillAnalysis/components/cutfillcom'



	export default {
		name: "SpatialCom",
		mixins: [basemixins.DATA_ISSHOW, basemixins.COMPONENTS_PARTCOMWITHBORDER],
		components: {skyLine,sectionanalysis, limitHeight, visualField,visualAnalysis, sunLight,cutfillcom},


        props:['winWidth'],

		data() {
			return {


				subcurTool: null,
                subtools:[
                    {name:"天际线分析",refname:"skyLine",imgfilename:"SkylineAnalysis"},
					{name:"剖面分析",refname:"sectionanalysis",imgfilename:"sectionAnalysis"},
					{name:"限高分析",refname:"limitHeight",imgfilename:"HeightLimitAnalysis"},
					{name:"可视域分析",refname:"visualField",imgfilename:"VisualShedAnalysis"},
					{name:"通视分析",refname:"visualAnalysis",imgfilename:"VisualAnalysis"},
					{name:"日照分析",refname:"sunLight",imgfilename:"SunAnalysis"},
					{name:"填挖方分析",refname:"cutfillcom",imgfilename:"cutFillDraw"},
                ]
			}
		},
        computed: {

        },
        mounted() {

        },
		methods: {
			ShowMenu(){
				$("#SpatialCommon #SubMainFrame").css('display', 'block');
				// $("#SpatialCommon #SubMainFrame").animate({height: '268px'});
				$("#SpatialCommon #SubMainFrame").animate({height: '308px'});
            },
			HiddenMenuWithAnamate(){
				$("#SpatialCommon #SubMainFrame").animate({height: '0px'}, '400', function () {
					$("#SpatialCommon #SubMainFrame").css('display', 'none');
				});
				this.clearanasubmenus();
				this.$emit("close");
            },
            HiddenMenu(){
				$("#SpatialCommon #SubMainFrame").css('display', 'none');
				$("#SpatialCommon #SubMainFrame").css('height', '0px');
				this.clearanasubmenus();
            },


            clicksubmenu(subitem){
				this.clearanasubmenus();
                this.subcurTool=subitem;
                if(this.$refs[subitem.refname])
                {
					this.$refs[subitem.refname].isShow=true;
                }
            },
            clearanasubmenus(){
				this.subcurTool=null;
				this.subtools.forEach(subtool=>{
					if(this.$refs[subtool.refname]){
						this.$refs[subtool.refname].isShow=false;
					}
				})
            },





		},
		watch: {

		}
	}
</script>

<style scoped>

    #SpatialCommon #SubMainFrame {
        background: #122134cc;
        width: 34px;
        /*height: 268px;*/
        height:0px;
        top: 100px;
        right: 22px;
    }

    #SpatialCommon #SubMainFrame img {
        width: 20px;
        height: 20px;
        margin: 9px 7px;
        box-sizing: border-box;
        vertical-align: top;
    }

</style>

<style>
    @import "../assets/css/spaceAnalysis.css";
</style>

