<template>
    <div>
        <dialogcomwithborder id="SectionCom" title="剖面分析" class="position-fixed"
                             :width="352" :height="showdrawchart?350:124" :left="left" :top="100"
                             headertitlestyle="left:20px;transform:translate(0px,0px);line-height:40px;font-size:16px;font-weight:bold;"
                             v-show="isShow" :headhaloshow="false" :onclose-callback="close">
            <template v-slot:title>
                <img src="images/cesium/SpaceAnalysis/sectionAnalysis.svg"
                     class="float-left" style="width:20px;height:20px;margin:10px 8px 10px 0px;">
                <div class="float-left">剖面分析</div>
            </template>
            <div id="Content" :class="showdrawchart?'select':''">
                <div style="height:30px;width:160px;" class="margin-auto">
                    <el-button  :class="showdrawchart?'float-left select':'float-left'" style="margin-right:30px;width:100px;" @click="sectionAnalysis">绘制剖面</el-button>
                    <el-button  style="width:30px;" class="spatialanalysisclear float-left" @click="clearSection"></el-button>
                </div>
                <div id="SectionDrawContainer" class="width100percent height100percent"></div>
            </div>
        </dialogcomwithborder>
    </div>
</template>

<script>
	import dialogcomwithborder from '@/common/components/dialogcomwithborder'
	import basemixins from '@/common/buss/BaseMixins'
	import sectionanalysishandler from '../buss/SectionAnalysis'

	import Bus from '@/common/buss/Bus'
	import coordinate from '@/common/util/Coordinates'

	export default {
		name: "SectionCom",
		data() {
			return {


				startdrawsection: false,
				showdrawchart: false,
				leftclicknum: 0,
			}
		},
		created() {
			// sectionHandle.vueInstance = this;
		},
        props:['winWidth'],
		components: {dialogcomwithborder},
		mixins: [basemixins.DATA_ISSHOW],
        computed:{

            left:function () {
                return (this.winWidth - 34 - 20 - 352 - 22);
            }
        },
        mounted() {



			let that=this;
			Bus.VM.$on(Bus.SignalType.Scene_Mouse_Left_Click, function (movement) {
				if (!that.startdrawsection) return;
				if (that.leftclicknum % 2 == 0) {
					sectionanalysishandler.HandleMouseFirstClick(movement.position);
				}
				else if (that.leftclicknum % 2 == 1) {
					sectionanalysishandler.HandleMouseSecondClick(movement.position);
					that.showdrawchart = true;
					that.setdata(sectionanalysishandler.GetDrawEchartData());

				}
				that.leftclicknum++;
			});
			Bus.VM.$on(Bus.SignalType.Scene_Mouse_Move, function (movement) {
				if (that.leftclicknum % 2 == 1) {
					sectionanalysishandler.HandleMouseMove(movement.endPosition);
				}
			});
        },
		methods: {
			sectionAnalysis: function () {
				this.startdrawsection = true;
				this.leftclicknum = 0;

			},
			clearSection: function () {

				sectionanalysishandler.ClearEntity();
				this.leftclicknum = 0;
				this.showdrawchart = false;
				this.startdrawsection=false;


			},
			close() {
				this.isShow = false;
				this.$emit('onclose');
			},


			setdata(datalist) {
				let that = this;
				this.$nextTick(function () {
					that.refreshbar(datalist);
				})
			},
			refreshbar(data) {
				let barcontainer = document.getElementById("SectionDrawContainer");
				let xwidth=barcontainer.clientWidth-40-20;
				let yheight=barcontainer.clientHeight-60-60;
				let dis=coordinate.CoordinateWGS84.GetDistancePlane(
					new coordinate.CoordinateWGS84(data[0].longitude,data[0].latitude,0),
					new coordinate.CoordinateWGS84(data[data.length-1].longitude,data[data.length-1].latitude,0));
				let ydata=data.map(item => {
					return item.height;
				});
				// let ymax=parseInt(Math.max.apply(null,ydata));
				let ymax=parseInt(yheight*dis/xwidth);
				let barChart = echarts.init(barcontainer);

				let option = {
					title: {
						show: false
					},
					grid: {
						top: 20,
						bottom: 20,
						left:30,
						right:20
					},
					color: ['#ffcc00'],
					tooltip: {
						trigger: 'axis',
						formatter: function (params) {
							let param = data[params[0].axisValue];
							return "经度:"+(parseInt(param.longitude*100))/100+"</br>纬度:"+(parseInt(param.latitude*100))/100+"</br>高程:"+(parseInt(param.height*100))/100+"米";
						},
						axisPointer: {
							animation: false
						}
					},
					xAxis: {
						type: 'category',
						// data: ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun']
						data: data.map(item => {
							return item.id;
						}),
						axisLine: {
							lineStyle: {
								color: "#fff"
							}
						},
						axisLabel: {
							show: false
						},
						axisTick: {
							show: false
						}
					},
					yAxis: {
						type: 'value',
						axisLine: {
							lineStyle: {
								color: "#fff"
							}
						},
						min: 0,
						max:ymax
					},
					series: [{
						// data: [820, 932, 901, 934, 1290, 1330, 1320],
						data: ydata,
						type: 'line',
						showSymbol: false,
						hoverAnimation: false,
					}]
				};
				barChart.setOption(option, true);
			},
		},


		watch: {
			isShow(newval, oldval) {
				if (newval == false) {
					this.clearSection();
				}
			},
		}
	}
</script>

<style scoped>

</style>
<style>
    #SectionCom #Content {
        width: 290px;
        height: 30px;
        margin: auto;
        overflow: hidden;
    }
    #SectionCom #Content.select {
        height: 256px;
        margin: auto;
    }


    #SectionCom #Content button {
        height: 30px;
        line-height: 30px;
        padding: 0px;
        border-radius: 2px;
        border: none;
        background-color: #049effb2;
        color: white;
        box-sizing: border-box;
        margin-left: 0px;
    }
    #SectionCom #Content button.select{
        background-color: #049eff;
    }
    #SectionCom #Content #SectionDrawContainer{
        width: 100%;
        height:200px;
        margin-top:26px;
    }
</style>
