<template>
    <div>
        <div class="position-absolute northinButton">
            <img src="images/cesium/MainGIS/compass.svg" class="position-absolute" style="right:0px;">
            <img src="images/cesium/MainGIS/northin.svg" ref="northtin" class="position-absolute"
                 style="width: 34px; height: 34px; right: 0;" @click="northClick">
        </div>

        <div class="position-absolute zoomInButton">
            <img src="images/cesium/MainGIS/zoomIn.svg" @click="zoomIn">
        </div>

        <div class="position-absolute zoomOutButton">
            <img src="images/cesium/MainGIS/zoomOut.svg" @click="zoomOut">
        </div>

        <div class="position-absolute fullscreenButton">
            <img src="images/cesium/MainGIS/fullscreen.svg" @click="fullscreenClick">
        </div>
    </div>
</template>

<script>
    import cesiumViewer from '@/modules/cesium/common/buss/CesiumViewer'

    import coordinate from '@/common/util/Coordinates'


    export default {
        name: "Compass",
        data() {
            return {
                postRenderListener: null,
                viewer:null,
            }
        },
        props: {
            viewerContainer: {
                type: String,
                default: ""
            },
        },
        unmounted() {
            cesiumViewer.CesiumViewer.getInstance().deleteall();
        },
        mounted() {


            cesiumViewer.CesiumViewer.getInstance().initSceneViewer(this.viewerContainer);

            let that = this;
            this.postRenderListener = function () {
                let scenemode = cesiumViewer.CesiumViewer.getInstance().GetSceneMode();
                if (scenemode == 3 || scenemode == 1) {
                    let headingdeg = cesiumViewer.CesiumViewer.getInstance().GetCamera().heading;
                    that.$refs.northtin.style.transform = "rotate(" + headingdeg + "deg)";
                }
            }

            cesiumViewer.CesiumViewer.getInstance().AddPostRenderEventListener(this.postRenderListener);

            // 点击的经纬度坐标为:121.49370613440942,31.241454371245997 海拔高度:-0.23028454721294528m
            // CesiumViewer.js:158 当前相机位置为：x=121.49399253531634,y=31.23400744071082,z=993.2516494201265,heading=6.188217047134122,pitch=-0.9453427987712746,roll=-0.9453427987712746
            // cesiumViewer.CesiumViewer.getInstance().viewer.camera.setView({
            //     destination:Cesium.Cartesian3.fromDegrees(121.49399253531634,31.23400744071082,994),
            //     orientation: {
            //         heading : 6.188217047134122, // east, default value is 0.0 (north)
            //         pitch : -0.9453427987712746,    // default value (looking down)
            //         roll : 0.0                             // default value
            //     }
            // })
        },
        methods: {
            //指北
            northClick() {
                let camera = cesiumViewer.CesiumViewer.getInstance().GetCamera();
                let wgs84poi = new coordinate.CoordinateWGS84(camera.longitude, camera.latitude, camera.height);
                let wgs84poinew = cesiumViewer.CesiumViewer.getInstance().GetPointOnSameCircle(wgs84poi, camera.heading, 0, camera.pitch);
                // cesiumViewer.CesiumViewer.getInstance().FlyToWithDuration(wgs84poinew.latitude, wgs84poinew.longitude, camera.height, 0, camera.pitch, camera.roll, -1);
            },

            //放大
            zoomIn: function () {
                let Camera = cesiumViewer.CesiumViewer.getInstance().getCamera();
                if (Camera.height < 10000) return;
                if (Camera.height > 10000 && Camera.height < 30000) {
                    cesiumViewer.CesiumViewer.getInstance().viewer.camera.zoomIn(5000);
                } else {
                    cesiumViewer.CesiumViewer.getInstance().viewer.camera.zoomIn(50000);
                }
            },

            //缩小
            zoomOut: function () {
                cesiumViewer.CesiumViewer.getInstance().viewer.camera.zoomOut(50000);
            },

            //全屏
            fullscreenClick() {
                if (!document.fullscreenElement &&
                    !document.mozFullScreenElement && !document.webkitFullscreenElement && !document.msFullscreenElement) {
                    if (document.documentElement.requestFullscreen) {
                        document.documentElement.requestFullscreen();
                    } else if (document.documentElement.msRequestFullscreen) {
                        document.documentElement.msRequestFullscreen();
                    } else if (document.documentElement.mozRequestFullScreen) {
                        document.documentElement.mozRequestFullScreen();
                    } else if (document.documentElement.webkitRequestFullscreen) {
                        document.documentElement.webkitRequestFullscreen(Element.ALLOW_KEYBOARD_INPUT);
                    }
                }
            }
        }
    }
</script>

<style>
    .northinButton {
        bottom: 136px;
        right: 1vw;
        width: 34px;
        height: 34px;
        cursor: pointer;
    }

    .zoomInButton {
        bottom: 100px;
        right: 1vw;
        width: 34px;
        height: 34px;
        cursor: pointer;
    }

    .zoomOutButton {
        bottom: 64px;
        right: 1vw;
        width: 34px;
        height: 34px;
        cursor: pointer;
    }

    .fullscreenButton {
        bottom: 28px;
        right: 1vw;
        width: 34px;
        height: 34px;
        cursor: pointer;
    }
</style>
